import React, { Component } from 'react';
import "../css/Manage.css";
import {ImSpinner2} from "react-icons/im";
import { withAuth0 } from '@auth0/auth0-react';

class FileUpload extends Component {
  // If you are using TypeScript the state will be
  // const [file, setFile] = useState<FileList | null>(null);
  constructor() {
    super();
    this.state = {
      file: null,
      image: null,
      submit: false,
      empty: false,
      error: null
    };
  }

  submitFile = async (e) => {
    e.preventDefault();

    this.setState({
      submit: true
    })

    try {
      if (!this.state.file) {
        this.setState({
          submit: false,
          empty: true,
          error: null
        })
      } else{

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});
        
        const formData = new FormData();
        formData.append('file', this.state.file[0]);
        const result = await fetch(`/api/${this.props.collection}/uploadImage/${this.props.itemID}`, {
            method: 'post',
            headers: {
              Authorization: `Bearer ${token}`,
            },
            body: formData
        });
        // handle success
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
          this.setState({
            error: body.errors.msg,
            submit: false
          })
        }else{
          this.setState({
            file: null,
            submit: false
          });
          this.props.updateList(body.reverse());
        }
      }
    } catch (error) {
      // handle error
      this.setState({
        submit: false
      })
    }
  };
  
  render(){
    return (
      <>
        <form onSubmit={(e) => this.submitFile(e)} id="mngImgUploadForm">
          {this.state.error !== null? <div style={{color: "red"}}>{this.state.error}</div>: null}
          {this.state.empty? <div style={{color: "red"}}>Choose a file</div>: null}
          <label className="mngItemValue">Upload file: </label>
          <input type="file" onChange={event => this.setState({file: event.target.files})}/>
          <button type="submit"
          style={{pointerEvents: this.state.submit === true? "none": "auto", 
          opacity: this.state.submit === true? "0.5": "1"}}>
            {this.state.submit === false? "Upload": <ImSpinner2 className="iconSubmitLoad"/>}
          </button>
        </form>
      </>
    );
  }
};

export default withAuth0(FileUpload);