import React, {Component} from 'react';
import {FaWindowClose} from "react-icons/fa";
import ManagementNav from "../AdminComponents/ManagementNav";
import { withAuth0 } from '@auth0/auth0-react';

class ManageContact extends Component {
    _isMounted = false;

    constructor() {
        super();
        this.state = {
          contact: [],
          item: {},
          isLoading: true
        };
    
        this.deleteContactItem = this.deleteContactItem.bind(this);
        this.displayItem = this.displayItem.bind(this);
        this.closeItem = this.closeItem.bind(this);
      }

    displayItem(index){
        let tempItem = this.state.contact[index]

        this.setState({
            item: tempItem
        });
    }

    closeItem(){
        this.setState({
            item: {}
        });
    }


    deleteContactItem = async (id) => {

        this.closeItem()

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch(`/api/manage/delContact/${id}`, {
            method: 'delete',
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        this.setState({
            contact: body.reverse(),
        });
    }

    componentDidMount() {
        this._isMounted = true;

        const { getAccessTokenSilently } = this.props.auth0;
    
        const fetchData = async () => {
            const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

            const result = await fetch(`/api/manage/getContact`, {
                headers: { Authorization: `Bearer ${token}` }
            },);
            const body = await result.json();
            
            this.setState({
                contact: body.reverse(),
                isLoading: false
            });
        }
        fetchData()
    }

    componentWillUnmount() {
        this._isMounted = false;
    }
    

    render(){
        return(
            <>
                <ManagementNav/>
                <h1 id="mngHeader">Contact Us Management</h1>
                
                <h2 className="mngSubHeader">Manage Contact Us</h2>
                <div id="container">
                    <table>
                        <thead>
                        <tr>
                            <th>Name</th>
                            <th>Email</th>
                            <th>Subject</th>
                            <th>Submitted On</th>
                        </tr>
                        </thead>
                        <tbody>
                            {this.state.contact.map((item, key) => (
                                <tr key={key} onClick={() => this.displayItem(this.state.contact.indexOf(item))}> 
                                    <td>{item.name}</td>
                                    <td>{item.email}</td>
                                    <td>{item.subject}</td>
                                    <td>{item.contactTime}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>

                {Object.keys(this.state.item).length !== 0?
                <div className="mngOrderWrapper" onClick={() => this.closeItem()}>
                    <div className="mngOrderWindow">
                        <FaWindowClose className="mngOWIcon" onClick={() => this.closeItem()}/>
                        <div><span className="mngItemValue">Submitted On:</span> {this.state.item.contactTime}</div>
                        <div><span className="mngItemValue">Name:</span> {this.state.item.name}</div>
                        <div><span className="mngItemValue">Email:</span> {this.state.item.email}</div>
                        <div><span className="mngItemValue">Subject:</span> {this.state.item.subject}</div>
                        <br/>
                        <div><span className="mngItemValue">Message:</span> {this.state.item.message}</div> 
                        <button className="mngDelete" onClick={() => this.deleteContactItem(this.state.item._id)}>Delete</button>
                    </div>  
                </div>
                :null}

                <div id="mngbottomSpace"></div>
            </>
        )
    }
}


export default withAuth0(ManageContact);