import React, {Component} from 'react';
import "../css/Manage.css";
import ManagementNav from "../AdminComponents/ManagementNav";
import CustomOrderWindow from "../AdminComponents/CustomOrderWindow";
import CateringOrderWindow from "../AdminComponents/CateringOrderWindow";
import OrdersWindow from "../AdminComponents/OrdersWindow";
import { withAuth0 } from '@auth0/auth0-react';

class Manage extends Component {
    _isMounted = false;

    constructor() {
        super();
        this.state = {
          regularOrders: [],
          customOrders: [],
          cateringOrders: [],
          isLoading: true
        };

        this.updateOrders = this.updateOrders.bind(this);
      }

    updateOrders(orders){
        this.setState({
            regularOrders: orders[0].reverse(),
            customOrders: orders[1].reverse(),
            cateringOrders: orders[2].reverse()
        });
    }

    componentDidMount() {
      this._isMounted = true;

      const { getAccessTokenSilently } = this.props.auth0;

      const fetchData = async () => {
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch(`/api/manage/getOrders`, {
                headers: { Authorization: `Bearer ${token}` }
            },)
        const body = await result.json();

        if (this._isMounted) {
          this.setState({
            regularOrders: body[0].reverse(),
            customOrders: body[1].reverse(),
            cateringOrders: body[2].reverse(),
            isLoading: false
          });
        }
      }
      fetchData()
    }

    componentWillUnmount() {
      this._isMounted = false;
    }

    render(){
        return( 
            <>
            <ManagementNav/>

            <h1 id="mngHeader">Orders Management</h1>

            <OrdersWindow
            regularOrders={this.state.regularOrders}
            updateOrders={this.updateOrders}
            />

            <CustomOrderWindow
            customOrders={this.state.customOrders}
            updateOrders={this.updateOrders}
            />

            <CateringOrderWindow
            cateringOrders={this.state.cateringOrders}
            updateOrders={this.updateOrders}
            />
            </>
        )
    }
}


export default withAuth0(Manage);