import React, {Component} from "react";
import logo from "../images/logo.png";
import { withRouter } from "react-router-dom";
import "../css/Checkout.css";
import {MdKeyboardArrowLeft} from "react-icons/md";
import {AiFillPlusCircle} from "react-icons/ai";
import {AiFillMinusCircle} from "react-icons/ai";
import {ImSpinner2} from "react-icons/im";
import moment from 'moment';
import * as EmailValidator from 'email-validator';
import { CardElement } from "@stripe/react-stripe-js";
import poweredByStripe from "../images/stripeLogo.png";

class CheckoutForm extends Component {

  constructor() {
    super();
    this.state = {
      firstName: '',
      lastName: '',
      address: ['','','','',''],
      email: '',
      phone: '',
      submit: false,
      touched: {},
      errors: {},
      bErrors: null,
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleArrayChange = this.handleArrayChange.bind(this);
    this.handlePayOnline = this.handlePayOnline.bind(this);
    this.handleCashOrder = this.handleCashOrder.bind(this);
    this.validate = this.validate.bind(this);
    this.scrollDiv = React.createRef();
  }

  handlePayOnline = async (e) => {
    e.preventDefault();

    this.setState({
      touched: { orderType: true, firstName: true, lastName: true, eventType: true, guestNum: true, 
          deliveryMethod: true, dateForOrder: true, address0: true, address1: true, address2: true, 
          address3: true, address4: true, email: true, phone: true, message: true,
      },
      bErrors: null,
      submit: true
    }, async () => {
      this.validate()

      if(Object.keys(this.state.errors).length === 0){
        const {stripe, elements} = this.props;

        const { error, paymentMethod } = await stripe.createPaymentMethod({
          type: "card",
          card: elements.getElement(CardElement),
        });

        //stripe--------
        if (!error) {

          //send token to backend here
          try {

            let delivery = 0;
            let total = 0;
            let delMethod = '';

            if(this.props.freeDeliveryValid){
              total = this.props.total;
              delivery = 0;
            } else if((this.props.freeDeliveryValid === null && this.props.deliverySelection) || 
            (!this.props.freeDeliveryValid && this.props.deliverySelection)) {
              total = this.props.total + this.props.deliveryAmount;
              delivery = this.props.deliveryAmount;
            } else{
              total = this.props.total;
              delivery = 0;
            }

            if(this.props.deliverySelection){
              delMethod = 'Delivery'
            } else {
              delMethod = 'Pick Up'
            }

            let cart = this.props.cart;
            cart.forEach(function(v){ delete v._id; delete v.itemDesc; });

            const { id } = paymentMethod;
            const response = await fetch("/api/order/handlePayOnline",
              {
                method: 'post',
                body: JSON.stringify({
                  id: id,
                  orderTime: moment().format('LLL'),
                  name: this.state.firstName + " " + this.state.lastName, 
                  deliveryMethod: delMethod,
                  paymentMethod: "Online Payment",
                  dateForOrder: this.props.deliveryDate,
                  address: this.state.address,
                  email: this.state.email,
                  phone: this.state.phone,
                  total: [this.props.total, delivery, total], 
                  cart: cart
                }),
                headers: {
                  'Content-Type': 'application/json'
                }
              }
            );

            const body = await response.json();

            if(body.hasOwnProperty('errors')){
              let nested = [];
              const errors1 = body.errors.map(error => {
                  if(!error.hasOwnProperty('nestedErrors')){
                      return error.msg;
                  } else {
                      nested = [...nested, ...error.nestedErrors];
                      return error.msg; 
                  }
              })
              const errors2 = nested.map(error => {
                  return error.msg
              })
              const errors = [...errors1,...errors2]
              this.setState({
                  submit: false,
                  bErrors: errors
              }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
            } else if (body.success === false){
              this.setState({
                submit: false,
                bErrors: [body.message]
            }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
            }else {
              this.setState({
                firstName: '',
                lastName: '',
                address: ['','','','',''],
                email: '',
                phone: '',
                bErrors: null
              });

              this.props.clearCart();
              this.props.confirmation(body.order, "regular");
              this.props.history.push("/confirmation");
            }
          } catch (error) {
            console.log(error);
            /*
            this.setState({
              bErrors: [error]
          }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
          */
          }
        } else {
          this.setState({
            submit: false,
            bErrors: [error.message]
          }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
        }
        //stripe------------
      } else {
        this.setState({
          submit: false
        })
      }
    })
  };

  handleCashOrder = async (e) => {
    e.preventDefault();

    this.setState({
      touched: { orderType: true, firstName: true, lastName: true, eventType: true, guestNum: true, 
        deliveryMethod: true, dateForOrder: true, address0: true, address1: true, address2: true, 
        address3: true, address4: true, email: true, phone: true, message: true,
      },
    }, async () => {
        this.validate()

        if((Object.keys(this.state.errors).length === 0) ){
          let delivery = 0;
          let total = 0;
          let delMethod = '';

          if(this.props.freeDeliveryValid){
            total = this.props.total;
            delivery = 0;
          } else if((this.props.freeDeliveryValid === null && this.props.deliverySelection) || 
          (!this.props.freeDeliveryValid && this.props.deliverySelection)) {
            total = this.props.total + this.props.deliveryAmount;
            delivery = this.props.deliveryAmount;
          } else{
            total = this.props.total;
            delivery = 0;
          }

          if(this.props.deliverySelection){
            delMethod = 'Delivery'
          } else {
            delMethod = 'Pick Up'
          }

          let cart = this.props.cart;
          cart.forEach(function(v){ delete v._id; delete v.itemDesc; });

          const result = await fetch('/api/order/handleCashOrder', {
              method: 'post',
              body: JSON.stringify({
                orderTime: moment().format('LLL'),
                name: this.state.firstName + " " + this.state.lastName, 
                deliveryMethod: delMethod,
                paymentMethod: "In Person",
                dateForOrder: this.props.deliveryDate,
                address: this.state.address,
                email: this.state.email,
                phone: this.state.phone,
                total: [this.props.total, delivery, total], 
                cart: cart
              }),
              headers: {
                  'Content-Type': 'application/json'
              }
            });

            const body = await result.json();

            if(body.hasOwnProperty('errors')){
              let nested = [];
              const errors1 = body.errors.map(error => {
                  if(!error.hasOwnProperty('nestedErrors')){
                      return error.msg;
                  } else {
                      nested = [...nested, ...error.nestedErrors];
                      return error.msg; 
                  }
              })
              const errors2 = nested.map(error => {
                  return error.msg
              })
              const errors = [...errors1,...errors2]
  
              this.setState({
                  submit: false,
                  bErrors: errors
              }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
            } else {
              this.setState({
                firstName: '',
                lastName: '',
                address: ['','','','',''],
                email: '',
                phone: '',
                bErrors: null
              });

              this.props.clearCart();
              this.props.confirmation(body, "regular");
              this.props.history.push("/confirmation");
            }
        }
    })
  }

  handleChange(e) {
    this.setState({
        [e.target.name]: e.target.value,
        touched: {...this.state.touched, [e.target.name]: true}
    });
  }

  handleArrayChange(num, e){
      let stateName = e.target.name;
      let array = this.state[stateName];
      array[num] = e.target.value;

      this.setState({
          [e.target.name]: [...array],
          touched: {...this.state.touched, [`address${num}`]: true}
      });
  }

  validate(){
    const errors = {}

    if(this.state.touched.firstName){
        if (this.state.firstName.trim() === "") {
            errors.firstName = 'Required';
        } else if (this.state.firstName.length < 3) {
            errors.firstName = 'Must be at least 3 characters';
        } else if (this.state.firstName.length > 30) {
            errors.firstName = 'Must be less than 30 characters';
        } else if (!/^[A-Za-z\s]+$/.test(this.state.firstName)) {
            errors.firstName = 'Must contain letters only';
        }
    }

    if(this.state.touched.lastName){
        if (this.state.lastName.trim() === "") {
            errors.lastName = 'Required';
        } else if (this.state.lastName.length < 3) {
            errors.lastName = 'Must be at least 3 characters';
        } else if (this.state.lastName.length > 30) {
            errors.lastName = 'Must be less than 30 characters';
        } else if (!/^[A-Za-z\s]+$/.test(this.state.lastName)) {
            errors.lastName = 'Must contain letters only';
        }
    }

    if(this.props.deliverySelection){
        if(this.state.touched.address0){
            if (this.state.address[0].trim() === "") {
                errors.address0 = 'Required';
            } else if (this.state.address[0].length < 5) {
                errors.address0 = 'Must be at least 5 characters';
            } else if (this.state.address[0].length > 65) {
                errors.address0 = 'Must be less than 65 characters';
            } else if (!/^[A-Za-z0-9.,'\-\s]+$/.test(this.state.address[0])) {
                errors.address0 = 'Invalid Address';
            }
        }

        if (!/(^$)|(^[A-Za-z0-9.,'\-\s]+$)/.test(this.state.address[1])) {
            errors.address1 = 'Invalid character in use';
        } else if (this.state.address[1].length > 15) {
          errors.address1 = 'Must be less than 15 characters';
        }

        if(this.state.touched.address2){
            if (this.state.address[2].trim() === "") {
                errors.address2 = 'Required';
            } else if (this.state.address[2].length < 3) {
                errors.address2 = 'Must be at least 3 characters';
            } else if (this.state.address[2].length > 30) {
                errors.address2 = 'Must be less than 30 characters';
            } else if (!/^[A-Za-z.,'\-\s]+$/.test(this.state.address[2])) {
                errors.address2 = 'Invalid City';
            }
        }

        if(this.state.touched.address3){
            if (this.state.address[3].trim() === "") {
                errors.address3 = 'Required';
            } else if (this.state.address[3].length !== 2) {
                errors.address3 = 'Must be at 2 character (State abbreviation)';
            } else if (!/^[A-Za-z]+$/.test(this.state.address[3])) {
                errors.address3 = 'Must contain letters only';
            }
        }

        if(this.state.touched.address4){
            if (this.state.address[4].trim() === "") {
                errors.address4 = 'Required';
            } else if (this.state.address[4].length !== 5) {
                errors.address4 = 'Must be 5 digits';
            } else if (!/^[0-9]+$/.test(this.state.address[4])) {
                errors.address4 = 'Must Contain Numbers only';
            }
        }
    }

    if(this.state.touched.email){
        if (this.state.email.trim() === "") {
            errors.email = 'Required';
        } else if (this.state.email.length < 5) {
            errors.email = 'Must be at least 5 characters';
        } else if (this.state.email.length > 65) {
            errors.email = 'Must be less than 65 characters';
        }else if (!EmailValidator.validate(this.state.email)) {
            errors.email = 'Invalid email address';
        }
    }

    if(this.state.touched.phone){
        if (this.state.phone.trim() === "") {
            errors.phone = 'Required'
        } else if (this.state.phone < 10) {
            errors.phone = 'Must be at least 10 characters';
        } else if (!/^[+]?[1]?[\s]?[(]?[0-9]{3}[)]?[.-\s]?[0-9]{3}[.-\s]?[0-9]{4}$/.test(this.state.phone)) {
            errors.phone = 'Invalid Phone format'
        }
    }

    this.setState({
        errors: errors
    });
  }

  componentDidMount() {
      document.title = 'Checkout - The Recommended Dose';
  }

  render() {

    let style = {
      base: {
          fontFamily: 'Avenir',
          fontSize: '1.1em',
          backgroundColor: "white",
      },
      invalid: {
          color: '#fa755a',
          iconColor: '#fa755a'
      } 
    };

    return (
      <>
      <h1 id="coHeader">Checkout</h1>
      
      <h2 id="coDesc">
        {this.props.deliverySelection === "Pick Up"? 
        "Once you place your order we will contact you back for further instructions regarding your pick up":
        "Once you place your order we will contact you back to confirm your delivery"}
      </h2>

      <img id="coLogoImg" src={logo} alt="Logo" />
      <span id="coReturn" onClick={() => this.props.history.goBack()}>
        <MdKeyboardArrowLeft id="coReturnIcon"/> RETURN
      </span>

      {this.state.bErrors !== null?
        <div ref={this.scrollDiv} id="coBErrors">
          {this.state.bErrors
          .filter(error => !error.includes("Invalid value"))
          .map((error,key) => (
          <div key={key}> {error}</div>
          ))}
        </div>
      : null}

      <div id="coWrapper">
        <div  id="coDeliveryInfo" className="coInfoWrapper">
          <div id="coInfoContainer">
            <form id="coForm" onSubmit={this.props.paymentMethod? (e) => this.handlePayOnline(e): (e) => this.handleCashOrder(e)}>
              <h2 className="coHeader2">
                {this.props.deliverySelection? "Delivery": "Pick Up"} Details
              </h2>
              <div className="coTextboxDiv">
                <div>
                  <label>{this.props.deliverySelection? "Delivery": "Pick Up"} Date:</label>
                  <span>{this.props.deliveryDate}</span>
                </div>
              </div>
              <div className="coTextboxDiv">
                <div>
                  <label>First Name</label>
                  <input type="text" 
                  name="firstName"
                  value={this.state.firstName}
                  onChange={this.handleChange}
                  onBlur={this.validate}
                  required/>
                  {this.state.touched.firstName && this.state.errors !== undefined && this.state.errors.firstName !== undefined? 
                  <span id="coError" >{this.state.errors.firstName}</span>: null}
                </div>
                <div>
                  <label>Last Name</label>
                  <input type="text" 
                  name="lastName"
                  value={this.state.lastName}
                  onChange={this.handleChange}
                  onBlur={this.validate}
                  required/>
                  {this.state.touched.lastName && this.state.errors.lastName !== undefined? 
                  <span id="coError" >{this.state.errors.lastName}</span>: null}
                </div>
              </div>
              {this.props.deliverySelection?
              <>
                <div className="coTextboxDiv">
                  <div id="coAddress">
                    <label>Address</label>
                    <input type="text"  
                    name="address"
                    value={this.state.address[0]}
                    onChange={(e) => this.handleArrayChange(0, e)}
                    onBlur={this.validate}
                    required/>
                    {this.state.touched.address0 && this.state.errors.address0 !== undefined? 
                    <span id="coError" >{this.state.errors.address0}</span>: null}
                  </div>
                  <div id="coApt">
                    <label>Apt (Optional)</label>
                    <input type="text"  
                    name="address"
                    value={this.state.address[1]}
                    onChange={(e) => this.handleArrayChange(1, e)}
                    onBlur={this.validate}/>
                    {this.state.touched.address1 && this.state.errors.address1 !== undefined? 
                    <span id="coError" >{this.state.errors.address1}</span>: null}
                  </div>
                </div>
                <div id="coAddressDiv">
                  <div>
                    <label>City</label>
                    <input type="text"  
                    name="address"
                    value={this.state.address[2]}
                    onChange={(e) => this.handleArrayChange(2, e)}
                    onBlur={this.validate}
                    required/>
                    {this.state.touched.address2 && this.state.errors.address2 !== undefined? 
                    <span id="coError" >{this.state.errors.address2}</span>: null}
                  </div>
                  <div>
                    <label>State</label>
                    <input type="text"  
                    name="address"
                    value={this.state.address[3]}
                    onChange={(e) => this.handleArrayChange(3, e)}
                    onBlur={this.validate}
                    required/>
                    {this.state.touched.address3 && this.state.errors.address3 !== undefined? 
                    <span id="coError" >{this.state.errors.address3}</span>: null}
                  </div>
                  <div>
                    <label>Zip</label>
                    <input type="number"  
                    name="address"
                    value={this.state.address[4]}
                    onChange={(e) => this.handleArrayChange(4, e)}
                    onBlur={this.validate}
                    required/>
                    {this.state.touched.address4 && this.state.errors.address4 !== undefined? 
                    <span id="coError" >{this.state.errors.address4}</span>: null}
                  </div>
                </div>
              </>
              : null}
              {this.props.deliverySelection?
              <>
              <div className="coTextboxDiv">
                <div>
                  <label>Email</label>
                  <input type="email" 
                  name="email"
                  value={this.state.email}
                  onChange={this.handleChange}
                  onBlur={this.validate}
                  required/>
                  {this.state.touched.email && this.state.errors.email !== undefined? 
                  <span id="coError" >{this.state.errors.email}</span>: null}
                </div>
                <div>
                  <label>Phone</label>
                  <input type="number" 
                  name="phone"
                  value={this.state.phone}
                  onChange={this.handleChange}
                  onBlur={this.validate}
                  required/>
                  {this.state.touched.phone && this.state.errors.phone !== undefined? 
                  <span id="coError" >{this.state.errors.phone}</span>: null}
                </div>
              </div>
              </>:
              <>
              <div className="coTextboxDiv2">
                  <label>Email</label>
                  <input type="email" 
                  name="email"
                  value={this.state.email}
                  onChange={this.handleChange}
                  onBlur={this.validate}
                  required/>
                  {this.state.touched.email && this.state.errors.email !== undefined? 
                  <span id="coError" >{this.state.errors.email}</span>: null}
                </div>
                <div className="coTextboxDiv2">
                  <label>Phone</label>
                  <input type="number" 
                  name="phone"
                  value={this.state.phone}
                  onChange={this.handleChange}
                  onBlur={this.validate}
                  required/>
                  {this.state.touched.phone && this.state.errors.phone !== undefined? 
                  <span id="coError" >{this.state.errors.phone}</span>: null}
                </div>
              </>}

              {this.props.paymentMethod?
              <>
              <h2 className="coHeader3">Payment Details</h2>
              <div id="coPayment">
                <label>Card</label>
                <div>
                  <CardElement options = {{style: style}} hidePostalCode={false}/>
                </div>
                <a href="http://www.stripe.com/" target="_blank" rel="noopener noreferrer">
                  <img src={poweredByStripe} alt="Powered By Stripe" />
                </a>
              </div>
              </>
              :null}
              <button type="submit" id="coButton" 
              style={{pointerEvents: this.state.submit === true? "none": "auto", 
              opacity: this.state.submit === true? "0.5": "1"}}>
                {this.state.submit === false? "Place Order": <ImSpinner2 className="iconSubmitLoad"/>}
              </button>

              </form>
          </div>
        </div>

        <div id="coCartInfo" className="coInfoWrapper">
          <h2 className="coHeader3">Cart</h2>
          
          <div id="coCartOrdersWrapper">
            <div id="coCartOrders">
            {this.props.cart.map((cartItem, key) => (
              <div 
                className="coCartItem" 
                key={key} 
              >
                <div className="coCartItemInfo">
                  <span className="coCartItemName">{cartItem.itemName}</span>
                  <div className="coCartItemCtrl">
                    <span className="coCartItemPrice">${cartItem.price}</span> 
                    <span>x</span>
                    <AiFillMinusCircle 
                      className="coCartItemIcon"
                      onClick={cartItem.quantity !== 1? () => this.props.subQty(this.props.cart.indexOf(cartItem), cartItem.price): null}
                    />
                    <span className="coCartItemQty">{cartItem.quantity}</span>
                    <AiFillPlusCircle 
                      className="coCartItemIcon"
                      onClick={() => this.props.addQty(this.props.cart.indexOf(cartItem), cartItem.price)}
                    />
                  </div>
                  
                </div>
                <img src={cartItem.url} className="coCartItemImage" alt="Cart Item Pic"/>
              </div>
            ))}
            </div>
          </div>
          
          <div id="coTotalDiv">
            <div className="coCost">
              <label>Subtotal:</label>
              <span>${this.props.total}</span>
            </div>
            <div className="coCost">
              <label>Delivery Fee:</label>
              <span>{this.props.freeDeliveryValid? "FREE DELIVERY":
                (this.props.freeDeliveryValid === null && this.props.deliverySelection) || 
                (!this.props.freeDeliveryValid && this.props.deliverySelection)? 
                "$"+this.props.deliveryAmount: "$"+0}
              </span>
            </div>
            <div id="coTotal" className="coCost">
              <label>Total:</label>
              <span>${this.props.freeDeliveryValid? this.props.total:
                (this.props.freeDeliveryValid === null && this.props.deliverySelection) || 
                (!this.props.freeDeliveryValid && this.props.deliverySelection)? 
                this.props.total + this.props.deliveryAmount: this.props.total}
              </span>
            </div>
          </div>
        </div>
      </div>
      
      <div id="coBottomSpace"></div>

      </>
    );
  }
};

export default withRouter(CheckoutForm);