import React, { Component } from "react";
import "../css/Order.css";
import { Link } from "react-router-dom";
import moment from 'moment';

class Order extends Component {
  componentDidMount() {
    document.title = 'Order - The Recommended Dose';
  }
  render() {
    return (
      <div className="orderDiv">
        <Link to="/order/menu">
          <div className="orderOptionDiv">
            <span>Menu For:</span>
            <span>{moment(this.props.deliveryDate, 'll').format('MMM Do')}</span>
          </div>
        </Link>
        <Link to="/Order/customOrder">
          <div className="orderOptionDiv">Custom Order</div>
        </Link>
      </div>
    );
  }
}

export default Order;
