import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import "../css/Recipes.css";

class Recipes extends Component {

    constructor() {
        super();
        this.state = {
            recipes: []
        };
      }

    componentDidMount() {
        document.title = 'Recipes - The Recommended Dose';
        
        const fetchData = async () => {
          const result = await fetch(`/api/recipes/getRecipes`)
          const body = await result.json();
          this.setState({
            recipes: body.reverse()
          });
        }
        fetchData()
      }

    render(){
        return(
            <>
            <h1 id="rcpHeader">Recipes</h1>
            <div id="rcpWrapper">
            {this.state.recipes.map((item, key) => (
                  <Link key={key} to={`/recipes/${item.recipeName}`}>
                    <div className="rcpItem" >
                      <img src={item.url} alt={item.recipeName}/>
                      <span>{item.recipeName}</span>
                      {item.description !== ""? 
                        <span>{item.description !== undefined? 
                          <>
                          {item.description.substring(0,30)}
                          {item.description.length > 30? "...": null}
                          </>: null}</span>
                      :null}
                    </div>
                  </Link>
                ))}
            </div>

            <div id="rcpbottomSpace"></div>
            </>
        )
    }
}


export default Recipes;