import React, {Component} from 'react';
import "../css/FAQ.css";
import FAQSection from "../Components/FAQSection"

class FAQ extends Component {
    constructor() {
        super();
        this.state = {
          faq: []
        };
      }

    componentDidMount() {
        document.title = 'FAQ - The Recommended Dose';
        
        const fetchData = async () => {
          const result = await fetch(`/api/faq/getFAQ`)
          const body = await result.json();
          this.setState({
            faq: body
          });
        }
        fetchData()
      }
    render(){

        const delivery = this.state.faq.filter(item => item.category === "Delivery");
        const pickUp = this.state.faq.filter(item => item.category === "Pick Up");
        const ordering = this.state.faq.filter(item => item.category === "Ordering");
        const alAndNut = this.state.faq.filter(item => item.category === "Allergy And Nutrition");
        const other = this.state.faq.filter(item => item.category === "Other");


        return(
            <>
            <h1 id="faqHeader">Frequently Asked Questions</h1>

            <FAQSection section={delivery} title="Delivery"/>

            <FAQSection section={pickUp} title="Pick Up"/>

            <FAQSection section={ordering} title="Ordering"/>

            <FAQSection section={alAndNut} title="Allergy &amp; Nutrition"/>
            
            <FAQSection section={other} title="Other"/>
            
            <div id="mngbottomSpace"></div>
            </>
        )
    }
}


export default FAQ;