import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class CusConfirmation extends Component {

    render(){
        return(
            <>
            <h1 id="conHeader">Thank Your For Your Order!</h1>
            
            <div id="conWrapper">
              <div  id="conCOInfoWrapper" className="conInfoWrapper">
                <div id="conInfoContainer">
                  <div id="conForm">
                    <h2 className="conHeader2">
                      {this.props.body.deliveryMethod === "Delivery"? "Delivery": "Pick Up"} Details
                    </h2>
                    <div>
                        <label>Order ID: </label>
                        <span>{this.props.body._id}</span>
                    </div>
                    <div className="conTextboxDiv">
                      <div>
                        <label>Name</label>
                        <span>{this.props.body.name}</span>
                      </div>
                      <div>
                        <label>{this.props.body.deliveryMethod === "Delivery"? 
                          "Delivery": "Pick Up"} Date:
                        </label>
                        <span>{this.props.body.dateForOrder}</span>
                      </div>
                    </div>
                    <div className="conTextboxDiv">
                      <div>
                        <label>Email</label>
                        <span>{this.props.body.email}</span>
                      </div>
                      <div>
                        <label>Phone</label>
                        <span>{this.props.body.phone}</span>
                      </div>
                    </div>
                    {this.props.body.deliveryMethod === "Delivery"?
                    <>
                      <div className="conTextboxDiv">
                        <div id="conAddress">
                          <label>Address</label>
                          <span>{this.props.body.address[0]}</span>
                          {this.props.body.address[1] !== ""?
                          <span>{this.props.body.address[1]}</span>
                          : null}
                          <span>{this.props.body.address[2]}, {this.props.body.address[3]} {this.props.body.address[4]} </span>
                        </div>
                      </div>
                    </>
                    : null}
                    
                    <h2 id="conSummaryHeader" className="conHeader2">
                      Order Summary
                    </h2>
                    <div id="conCartOrders">
                    {this.props.body.orderType === "Cake"?
                        <>  
                        <div><label>Type of Cake:</label> {this.props.body.orderDetails[0]}</div>
                        <div><label>Cake  
                            {this.props.body.orderDetails[0] === "Standard Cake"? " Size":
                            this.props.body.orderDetails[0] === "Shape Cake"? " Shape":
                            this.props.body.orderDetails[0] === "Letter Cake"? " Letter":
                            this.props.body.orderDetails[0] === "Number Cake"? " Number": null}:</label> 
                            {this.props.body.orderDetails[0] === "Letter Cake"? " "+this.props.body.orderDetails[1].toUpperCase()
                            :" "+this.props.body.orderDetails[1]}
                        </div>
                        <div><label>Colors:</label> {this.props.body.orderDetails[2]}</div>
                        <div><label>Additional Details:</label> {this.props.body.orderDetails[3]}</div>
                        </>                                
                        : <div><label>Message:</label> {this.props.body.orderDetails}</div>}
                    </div>
                    
                    <Link to="/">
                        <button id="conButton">Return To Home</button>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
            </> 
        )
    }
}


export default CusConfirmation;