import React, {Component} from 'react';
import {Link, withRouter} from 'react-router-dom';
import logo from "../images/logo.png";
import { GiHamburgerMenu } from "react-icons/gi";
import { AiOutlineClose } from "react-icons/ai";
import { FiInstagram } from "react-icons/fi";
import { AiFillShopping } from "react-icons/ai";
import { withAuth0 } from '@auth0/auth0-react';
import moment from 'moment';


class Nav extends Component {
    
      handleOpen(){
        this.props.handleNavWindow(true);
        this.props.toggleScroll(true);
      }
      
      handleClose() {
        this.props.handleNavWindow(false);
        this.props.toggleScroll(false);
      }

      handleCartWindow(){
        this.props.handleCartWindow(true);
        this.props.toggleScroll(true);
        this.props.handleDeliverySelection(null);
        this.props.handlePaymentMethod(null);
      }

    render(){
        let visibility = "hide";

        if (this.props.navWindow) {
          visibility = "show";
        }

        const { logout, isAuthenticated } = this.props.auth0;

        return(
          <nav id="navBar">
            <div id="logoDiv">
              <div id="logo">
                <Link to="/">
                  <img id="logoImg" src={logo} alt="Logo" />
                </Link>
              </div>
              
              <GiHamburgerMenu id="navOpenIcon" onClick={() => this.handleOpen()} />
              
              <a
                id="igLinkTag"
                target="_blank"
                href="https://www.instagram.com/therecommendeddose/"
                rel="noopener noreferrer"
              >
                <FiInstagram id="igIcon" />
              </a>
            </div>
            <ul 
            id="navItems" 
            className={visibility}>
              <li>
               <AiOutlineClose id="navCloseIcon" onClick={() => this.handleClose()}/>
              </li>
              <li id="orderNav">
                {this.props.menuPageVis? (
                  <>
                  <Link to="/order" onClick={() => this.handleClose()}>
                  Order
                  </Link>
                  <div id="orderOpt">
                    <Link to="/order/menu"><span id="test">Menu For {moment(this.props.deliveryDate, 'll').format('MMM Do')}</span></Link>
                    <Link to="/order/customOrder"><span>Custom Order</span></Link>
                  </div>
                  </>
                ): 
                  <Link to="/order/customOrder" onClick={() => this.handleClose()}>
                    Order
                  </Link>
                }
              </li>
              <li>
                <Link to="/about" onClick={() => this.handleClose()}>
                  About
                </Link>
              </li>
              <li>
                <Link to="/catering" onClick={() => this.handleClose()}>
                  Catering
                </Link>
              </li>
              <li>
                <Link to="/contact" onClick={() => this.handleClose()}>
                  Contact
                </Link>
              </li>
              <li>
                <Link to="/recipes" onClick={() => this.handleClose()}>
                  Recipes
                </Link>
              </li>
              <li>
                <Link to="/faq" onClick={() => this.handleClose()}>
                  FAQ
                </Link>
              </li>
              {isAuthenticated?
              <>
              <li>
                <Link to="/manage" onClick={() => this.handleClose()}>
                  Manage
                </Link>
              </li>
              <li>
                <div style={{ cursor: "pointer" }} onClick={() => logout({})}>
                  Logout
                </div>
              </li>
              </>: null}
            </ul>
            {this.props.menuPageVis && window.location.pathname !== "/checkout" ?
              <div id="cartIconDiv">
                <AiFillShopping id="cartIcon" onClick={() => this.handleCartWindow()}/>
                <span>{this.props.itemsAmount}</span>
              </div>: null}
          </nav>
        )
    }
}


export default withRouter(withAuth0(Nav));