import React, {Component} from 'react';
import { withRouter } from "react-router-dom";
import "../css/ContactUs.css";
import {SiMinutemailer} from "react-icons/si";
import {BsClockHistory} from "react-icons/bs";
import {GoLocation} from "react-icons/go";
import {BiMessage} from "react-icons/bi";
import moment from 'moment';
import {ImSpinner2} from "react-icons/im";
import * as EmailValidator from 'email-validator';

class ContactUs extends Component {
    constructor() {
        super();
        this.state = {
            firstName: '',
            lastName: '',
            email: '',
            subject: '',
            message: '',
            touched: {},
            errors: {},
            submit: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.addCUMessage = this.addCUMessage.bind(this);
        this.validate = this.validate.bind(this);
        this.scrollDiv = React.createRef();
    }

    addCUMessage = async (e) => {
        e.preventDefault();

        this.setState({
            touched: { firstName: true, lastName: true, 
                email: true, subject: true, message: true,
            },
            submit: true
        }, async () => {
            this.validate();

            if(Object.keys(this.state.errors).length === 0){
                const result = await fetch('/api/contact/addContactItem', {
                    method: 'post',
                    body: JSON.stringify({
                        contactTime: moment().format('LLL'),
                        name: this.state.firstName + " " + this.state.lastName,
                        email: this.state.email,
                        subject: this.state.subject,  
                        message: this.state.message,
                    }),
                    headers: {
                        'Content-Type': 'application/json'
                    }
                });
                const body = await result.json();

                if(body.hasOwnProperty('errors')){
                    const errors = body.errors.map(error => {
                        return error.msg;
                    })
                    this.setState({
                        bErrors: errors,
                        submit: false
                    }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
                } else {
                    this.setState({
                        firstName: '',
                        lastName: '',
                        email: '',
                        subject: '',
                        message: '',
                        touched: {},
                        bErrors: undefined,
                        submit: false 
                    });

                    this.props.confirmation(body, "contact");
                    this.props.history.push("/confirmation");
                }
            } else {
                this.setState({
                    submit: false
                })
            }
        })
    }

    handleChange(e) {
        this.setState({
            [e.target.name]: e.target.value,
            touched: {...this.state.touched, [e.target.name]: true}
        });
    }

    componentDidMount() {
        document.title = 'Contact Us - The Recommended Dose';
    }

    validate(){
        const errors = {}
        
        if(this.state.touched.firstName){
            if (this.state.firstName.trim() === "") {
            errors.firstName = 'Required';
            } else if (this.state.firstName.length < 3) {
            errors.firstName = 'Must be at least 3 characters';
            } else if (this.state.firstName.length > 30) {
                errors.firstName = 'Must be less than 30 characters';
            } else if (!/^[A-Za-z\s]+$/.test(this.state.firstName)) {
                errors.firstName = 'Must contain letters only';
            }
        }

        if(this.state.touched.lastName){
            if (this.state.lastName.trim() === "") {
                errors.lastName = 'Required';
            } else if (this.state.lastName.length < 3) {
                errors.lastName = 'Must be at least 3 characters';
            } else if (this.state.lastName.length > 30) {
                errors.lastName = 'Must be less than 30 characters';
            } else if (!/^[A-Za-z\s]+$/.test(this.state.lastName)) {
                errors.lastName = 'Must contain letters only';
            }
        }

        if(this.state.touched.email){
            if (this.state.email.trim() === "") {
                errors.email = 'Required';
            } else if (this.state.email.length < 5) {
                errors.email = 'Must be at least 5 characters';
            } else if (this.state.email.length > 65) {
                errors.email = 'Must be less than 65 characters';
            }else if (!EmailValidator.validate(this.state.email)) {
                errors.email = 'Invalid email address';
            }
        }

        if(this.state.touched.subject){
            if (this.state.subject.trim() === "") {
                errors.subject = 'Required';
            } else if (this.state.subject.length < 3) {
                errors.subject = 'Must be at least 3 characters';
            } else if (this.state.subject.length > 30) {
                errors.subject = 'Must be less than 30 characters';
            } else if (!/^[A-Za-z\s]+$/.test(this.state.subject)) {
                errors.subject = 'Must contain letters only';
            }
        }

        if(this.state.touched.message){
            if (this.state.message.trim() === "") {
                    errors.message = 'Required';
            } else if (this.state.message.length < 10) {
                    errors.message = 'Must be at least 10 characters';
            } else if (this.state.message.length > 300) {
                    errors.message = 'Must be less than 300 characters';
            } else if (!/^[A-Za-z0-9\W\s]+$/.test(this.state.message)) {
                    errors.message = 'Invalid Message';
            }
        }

        this.setState({
            errors: errors
        });
      }

    render(){
        return(
            <>
            <h1 id="cuHeader">Contact Us</h1>

            {this.state.bErrors !== undefined?
                <div ref={this.scrollDiv} id=" cuBErrors">{this.state.bErrors.map((error,key) => (
                    <div key={key}> {error}</div>
                ))}
                </div>
            : null}

            <div id="cuWrapper">

                <div id="cuDescDiv">
                    <h2>Reach out to us at therecommendeddose@gmail.com, 
                    (347) 254-5895 or complete the form and we'll get back to 
                    you as soon as possible.</h2>
                    <BiMessage id="cuDescIcon"/> 
                </div>

                <div  className="cuFormWrapper">
                    <div className="cuFormContainer">
                        <form id="cuForm" onSubmit={(e) => this.addCUMessage(e)}>
                            <div className="cuDiv">
                                <div className="cuDiv2">
                                    <label className="cuLabel">First Name</label>
                                    <input
                                    type="text" 
                                    className="cuTextbox"
                                    name="firstName"
                                    value={this.state.firstName}
                                    onChange={this.handleChange}
                                    onBlur={this.validate}
                                    required/>
                                    {this.state.touched.firstName && this.state.errors !== undefined? 
                                    <span>{this.state.errors.firstName}</span>: null}
                                </div>
                                <div className="cuDiv2">
                                    <label className="cuLabel">Last Name</label>
                                    <input
                                    type="text" 
                                    className="cuTextbox"
                                    name="lastName"
                                    value={this.state.lastName}
                                    onChange={this.handleChange}
                                    onBlur={this.validate}
                                    required/>
                                    {this.state.touched.lastName && this.state.errors !== undefined? 
                                    <span>{this.state.errors.lastName}</span>: null}
                                </div>
                            </div>
                            <div className="cuDivST">
                                <label className="cuLabel">Email</label>
                                <input
                                type="email" 
                                className="cuTextbox"
                                name="email"
                                value={this.state.email}
                                onChange={this.handleChange}
                                onBlur={this.validate}
                                required/>
                                {this.state.touched.email && this.state.errors !== undefined? 
                                <span>{this.state.errors.email}</span>: null}
                            </div>
                            <div className="cuDivST">
                                <label className="cuLabel">Subject</label>
                                <input
                                type="text" 
                                className="cuTextbox"
                                name="subject"
                                value={this.state.subject}
                                onChange={this.handleChange}
                                onBlur={this.validate}
                                required/>
                                {this.state.touched.subject && this.state.errors !== undefined? 
                                <span>{this.state.errors.subject}</span>: null}
                            </div>

                            <div className="cuTextAreaDiv">
                                <label>Message</label>
                                <textarea
                                type="text" 
                                className="cuTextArea"
                                name="message"
                                value={this.state.message}
                                onChange={this.handleChange}
                                onBlur={this.validate}
                                required/>
                                {this.state.touched.message && this.state.errors !== undefined? 
                                <span>{this.state.errors.message}</span>: null}
                            </div>

                            <button type="submit" id="cuButton"
                            style={{pointerEvents: this.state.submit === true? "none": "auto", 
                            opacity: this.state.submit === true? "0.5": "1"}}>
                                {this.state.submit === false? "Submit": <ImSpinner2 className="iconSubmitLoad"/>}
                            </button>
                        </form>
                    </div>
                </div>
            </div>
            <div id="cuIconWrapper">
                <div className="cuIconDiv">
                    <SiMinutemailer className="cuIcons"/>
                    <p className="cuIconDesc">Friday - Sunday</p>
                </div>
                <div className="cuIconDiv">
                    <BsClockHistory className="cuIcons"/>
                    <p className="cuIconDesc">therecommendeddose@gmail.com</p>
                    <p className="cuIconDesc">(347) 254-5895</p>
                </div>
                <div className="cuIconDiv">
                    <GoLocation className="cuIcons"/>   
                    <p className="cuIconDesc">Bronx, New York</p>
                </div>
            </div>
            </>
        )
    }
}


export default withRouter(ContactUs);