import React, { Component } from "react";
import "./css/App.css";
import { Route, HashRouter as Router, Switch, withRouter} from "react-router-dom";
import Home from "./Pages/Home";
import About from "./Pages/About";
import Menu from "./Pages/Menu";
import Order from "./Pages/Order";
import CustomOrder from "./Pages/CustomOrder";
import Catering from "./Pages/Catering";
import ContactUs from "./Pages/ContactUs";
import Recipes from "./Pages/Recipes";
import RecipeArticle from "./Pages/RecipeArticle";
import FAQ from "./Pages/FAQ";
import Confirmation from "./Pages/Confirmation";
import PageNotFound from "./Pages/PageNotFound";
import Manage from "./AdminPages/Manage";
import ManageMenu from "./AdminPages/ManageMenu";
import ManageFAQ from "./AdminPages/ManageFAQ";
import ManageContact from "./AdminPages/ManageContact";
import ManageRecipes from "./AdminPages/ManageRecipes";
import ManageAbout from "./AdminPages/ManageAbout";
import ManageOther from "./AdminPages/ManageOther";
import Login from "./Pages/Login";
import Nav from "./Components/Nav";
import Footer from "./Components/Footer";
import Cart from "./Components/Cart";
import ProtectedRoute from "./auth/protected-route";
import StripeContainer from "./Components/StripeContainer";

class App extends Component {
  _isMounted = false;

  constructor() {
    super();
    this.state = {
      toggleScroll: false,
      navWindow: false,
      cartWindow: false,
      menuWindow: false,
      deliverySelection: null,
      paymentMethod: null,
      cart: [],
      total: 0,
      itemsAmount: 0,
      menuPageVis: null,
      deliveryAmount: 0,
      orderMin: 0,
      freeDeliveryMin: 0,
      freeDeliveryValid: null,
      deliveryDate: '',
      blockedDates: [],
    };

    this.confirmation = this.confirmation.bind(this);
    this.toggleScroll = this.toggleScroll.bind(this);
    this.closeWindows = this.closeWindows.bind(this);
    this.addToCart = this.addToCart.bind(this);
    this.clearCart = this.clearCart.bind(this);
    this.handleDeliverySelection = this.handleDeliverySelection.bind(this);
    this.handlePaymentMethod = this.handlePaymentMethod.bind(this);
    this.handleFreeDeliveryValid = this.handleFreeDeliveryValid.bind(this);
    this.handleFreeDeliveryValid2 = this.handleFreeDeliveryValid2.bind(this);
    this.handleNavWindow = this.handleNavWindow.bind(this);
    this.handleCartWindow = this.handleCartWindow.bind(this);
    this.handleMenuWindow = this.handleMenuWindow.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.addQty = this.addQty.bind(this);
    this.subQty = this.subQty.bind(this);
  }
  
  confirmation(body, type){
    this.setState({
      confirmationBody: body[0],
      confirmationType: type
    });
  }

  toggleScroll(vis){
    this.setState({
      toggleScroll: vis,
    });
  }

  closeWindows(){
    this.setState({
      toggleScroll: false,
      navWindow: false,
      cartWindow: false,
      menuWindow: false,
    });

    if(this.state.cartWindow){
      this.setState({
        deliverySelection: null
      });
    }
  }

  handleDeliverySelection(sel){
    this.setState({
      deliverySelection: sel
    });
  }

  handlePaymentMethod(sel){
    this.setState({
      paymentMethod: sel
    });
  }

  handleFreeDeliveryValid(){
    if (this.state.freeDeliveryMin === 0){
      this.setState({
        freeDeliveryValid: null
      });
    } else if ((this.state.freeDeliveryMin > 0) && (this.state.total >= this.state.freeDeliveryMin)){
      this.setState({
        freeDeliveryValid: true
      });
    } else if ((this.state.freeDeliveryMin > 0) && (this.state.total < this.state.freeDeliveryMin)) {
      this.setState({
        freeDeliveryValid: false
      });
    }
  }

  handleFreeDeliveryValid2(total){
    if (this.state.freeDeliveryMin === 0){
      this.setState({
        freeDeliveryValid: null
      });
    } else if ((this.state.freeDeliveryMin > 0) && (total >= this.state.freeDeliveryMin)){
      this.setState({
        freeDeliveryValid: true
      });
    } else if ((this.state.freeDeliveryMin > 0) && (total < this.state.freeDeliveryMin)) {
      this.setState({
        freeDeliveryValid: false
      });
    }
  }

  handleNavWindow(vis){
    this.setState({
      navWindow: vis
    });
  }

  handleCartWindow(vis){
    this.setState({
      cartWindow: vis,
    });

    this.handleFreeDeliveryValid()
  }

  handleMenuWindow(vis){
    this.setState({
      menuWindow: vis
    });
  }

  addToCart(tempItem, qty){

    const menuItem={
      _id: tempItem._id,
      itemName: tempItem.itemName,
      price: tempItem.price,
      itemDesc: tempItem.itemDesc,
      url: tempItem.url,
      quantity: qty
    }

    if(!this.state.cart.some(item => item._id === tempItem._id)){
      this.setState({
        cart: [...this.state.cart, menuItem],
        total: this.state.total + (menuItem.quantity * menuItem.price),
        itemsAmount: this.state.itemsAmount + menuItem.quantity
      });
    }else{
      let array = [...this.state.cart];
      const index = array.findIndex(item => item._id === tempItem._id);
      array[index].quantity += qty;

      this.setState({
        cart: array,
        total: this.state.total + (menuItem.quantity * menuItem.price),
        itemsAmount: this.state.itemsAmount + menuItem.quantity
      });
    }
  }

  clearCart(){
    this.setState({
      cart: [],
      total: 0,
      itemsAmount: 0,
      deliverySelection: null,
      paymentMethod: null
    });
  }

  deleteItem(index,price,qty){
    let array = [...this.state.cart];
    array.splice(index, 1);

    this.setState({
      cart: array,
      total: this.state.total - (price * qty),
      itemsAmount: this.state.itemsAmount - qty
    });

    this.handleFreeDeliveryValid2(this.state.total - (price * qty))
  }

  addQty(index, price){
    let array = [...this.state.cart];
    array[index].quantity += 1;

    this.setState({
      cart: array,
      itemsAmount: this.state.itemsAmount + 1,
      total: this.state.total + price
    });

    this.handleFreeDeliveryValid2(this.state.total + price)
  }

  subQty(index, price){
    let array = [...this.state.cart];

    if(array[index].quantity >= 1){
      array[index].quantity -= 1;

      this.setState({
        cart: array,
        itemsAmount: this.state.itemsAmount - 1,
        total: this.state.total - price
      });

      if(array[index].quantity === 0){
        this.deleteItem(index, price, 1);
      }
    }

    this.handleFreeDeliveryValid2(this.state.total - price)
  }

  componentDidMount() {
    this._isMounted = true;

    const fetchData = async () => {
        const result = await fetch(`/api/home/getOtherSettings`)
        const body = await result.json();

        if(this._isMounted){
          const mpv = body.filter(obj => {return obj.name === "MenuPageToggle"});
          if(mpv.length !== 0){
            this.setState({
                menuPageVis: mpv[0].toggle
              });
          }

          const da = body.filter(obj => {return obj.name === "DeliveryAmount"});
          if(da.length !== 0){
            this.setState({
                deliveryAmount: da[0].amount
              });
          }

          const om = body.filter(obj => {return obj.name === "OrderMin"});
          if(om.length !== 0){
            this.setState({
                orderMin: om[0].minimum
              });
          }

          const fdm = body.filter(obj => {return obj.name === "FreeDeliveryMin"});
          if(fdm.length !== 0){
            this.setState({
                freeDeliveryMin: fdm[0].minimum
              });
          }

          const dd = body.filter(obj => {return obj.name === "DeliveryDate"});
          if(dd.length !== 0){
            this.setState({
                deliveryDate: dd[0].date
              });
          }

          const bd = body.filter(obj => {return obj.name === "BlockedDates"});
          if(bd.length !== 0){
            this.setState({
                blockedDates: bd[0].dates
              });
          }
        }
    }
    fetchData()

  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {

    let overlay = "hide";
    if(this.state.toggleScroll === true){
      document.body.style.overflowY = "hidden";
      overlay = "show";
    }else if(this.state.toggleScroll === false){
      document.body.style.overflowY = "auto";
    }

    return (
      <Router>
        <div id="wrapper">

          {this.state.cart.length > 0 && 
          this.state.deliverySelection !== null &&
          this.state.paymentMethod !== null &&
          this.state.total >= this.state.orderMin &&
          window.location.pathname === "/checkout"? null:
          <Nav 
            toggleScroll={this.toggleScroll}
            handleCartWindow={this.handleCartWindow}
            navWindow={this.state.navWindow}
            handleNavWindow={this.handleNavWindow}
            itemsAmount={this.state.itemsAmount}
            deliveryDate={this.state.deliveryDate}
            menuPageVis={this.state.menuPageVis}
            handleDeliverySelection={this.handleDeliverySelection}
            handlePaymentMethod={this.handlePaymentMethod}
          />}

          <Cart 
            toggleScroll={this.toggleScroll}
            cartWindow={this.state.cartWindow}
            handleCartWindow={this.handleCartWindow}
            cart={this.state.cart}
            deleteItem={this.deleteItem}
            total={this.state.total}
            addQty={this.addQty}
            subQty={this.subQty}
            deliveryAmount={this.state.deliveryAmount}
            orderMin={this.state.orderMin}
            freeDeliveryMin={this.state.freeDeliveryMin}
            freeDeliveryValid={this.state.freeDeliveryValid}
            handleFreeDeliveryValid = {this.handleFreeDeliveryValid}
            menuPageVis={this.state.menuPageVis}
            deliverySelection={this.state.deliverySelection}
            handleDeliverySelection={this.handleDeliverySelection}
            paymentMethod={this.state.paymentMethod}
            handlePaymentMethod={this.handlePaymentMethod}
          />

          <div id="overlay" className={overlay} onClick={() => this.closeWindows()}></div>

          <div id="page-body">

          <Switch>
            <Route path="/" exact render={(props) => (
                <Home 
                {...props}
                menuPageVis={this.state.menuPageVis}/>
            )}/>
            {this.state.menuPageVis? 
              <Route path="/order" exact render={(props) => (
                <Order {...props} deliveryDate={this.state.deliveryDate}/>
              )}/>
            : null}
            {this.state.menuPageVis? (
              <Route path="/order/menu" render={(props) => (
                <Menu 
                {...props} 
                menu={this.state.menu}
                toggleScroll={this.toggleScroll}
                addToCart={this.addToCart}
                menuWindow={this.state.menuWindow}
                handleMenuWindow={this.handleMenuWindow}
                deliveryDate={this.state.deliveryDate}
                orderMin={this.state.orderMin}
                freeDeliveryMin={this.state.freeDeliveryMin}/>
              )}/>
            ): null}
            <Route path="/order/customOrder" render={(props) => (
                <CustomOrder 
                {...props}
                confirmation={this.confirmation} 
                blockedDates={this.state.blockedDates}/>
            )}/>
            <Route path="/about" component={About} />
            <Route path="/catering" render={(props) => (
                <Catering 
                {...props}
                confirmation={this.confirmation} 
                blockedDates={this.state.blockedDates}/>
            )}/>
            <Route path="/contact" render={(props) => (
                <ContactUs
                {...props}
                confirmation={this.confirmation}/>
            )}/>
            <Route path="/recipes" exact render={(props) => (
              <Recipes {...props} selectRecipe={this.selectRecipe}/>
            )}/>
            <Route path="/recipes/*" exact component={RecipeArticle}/>  
            <Route path="/faq" component={FAQ} />
            {this.state.cart.length > 0 && 
            this.state.deliverySelection !== null &&
            this.state.paymentMethod !== null &&
            this.state.total >= this.state.orderMin? 
              <Route path="/checkout" exact render={(props) => (
                <StripeContainer {...props}
                confirmation={this.confirmation}
                clearCart={this.clearCart} 
                deliveryDate={this.state.deliveryDate}
                deliverySelection={this.state.deliverySelection}
                paymentMethod={this.state.paymentMethod}
                cart={this.state.cart}
                total={this.state.total}
                deliveryAmount={this.state.deliveryAmount}
                freeDeliveryValid={this.state.freeDeliveryValid}
                addQty={this.addQty}
                subQty={this.subQty}/>
              )}/>
            : null}
            {this.state.confirmationBody !== undefined &&
            this.state.confirmationType !== undefined?
              <Route path="/confirmation" exact render={(props) => (
                <Confirmation {...props}
                body={this.state.confirmationBody}
                type={this.state.confirmationType}
                />
              )}/>
            : null}  
            <ProtectedRoute path="/manage" exact component={Manage}/>  
            <ProtectedRoute path="/manage/menu" component={ManageMenu}/> 
            <ProtectedRoute path="/manage/faq" component={ManageFAQ}/>
            <ProtectedRoute path="/manage/recipes" component={ManageRecipes}/>
            <ProtectedRoute path="/manage/contact" component={ManageContact}/>
            <ProtectedRoute path="/manage/about" component={ManageAbout}/>
            <ProtectedRoute path="/manage/other" component={ManageOther}/>
            <Route path="/login" component={Login}/> 
            <Route component={PageNotFound}/>
          </Switch>
          </div>
          {this.state.cart.length > 0 && 
          this.state.deliverySelection !== null &&
          this.state.paymentMethod !== null &&
          this.state.total >= this.state.orderMin &&
          window.location.pathname === "/checkout"? null: <Footer/>}

        </div>
      </Router>
    );
  }
}

export default withRouter(App);
