import React, {Component} from 'react';
import { withRouter } from "react-router-dom";
import "../css/Catering.css";
import {SlideDown} from 'react-slidedown';
import 'react-slidedown/lib/slidedown.css';
import 'react-dates/lib/css/_datepicker.css';
import { SingleDatePicker} from 'react-dates';
import isSameDay from 'react-dates/src/utils/isSameDay';
import moment from 'moment';
import {ImSpinner2} from "react-icons/im";
import * as EmailValidator from 'email-validator';

class Catering extends Component {
    constructor() {
        super();
        this.state = {
            firstName: '',
            lastName: '',
            eventType: '',
            guestNum: '',
            deliveryMethod: '',
            dateForOrder: null,
            focused: false,
            address: ['','','',''],
            email: '',
            phone: '',
            message: '',
            touched: {},
            errors: {},
            submit: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleArrayChange = this.handleArrayChange.bind(this);
        this.addCateringOrder = this.addCateringOrder.bind(this);
        this.validate = this.validate.bind(this);
        this.scrollDiv = React.createRef();
    }

    addCateringOrder = async (e) => {
        e.preventDefault();

        this.setState({
            touched: { firstName: true, lastName: true, eventType: true, guestNum: true, 
                deliveryMethod: true, dateForOrder: true, address0: true, address1: true, 
                address2: true, address3: true, email: true, phone: true, message: true,
            },
            submit: true
        }, async () => {
            this.validate()

            if((Object.keys(this.state.errors).length === 0) 
            && this.state.dateForOrder !== null){
                const result = await fetch('/api/catering/addCateringOrder', {
                    method: 'post',
                    body: JSON.stringify({
                        orderTime: moment().format('LLL'),
                        name: this.state.firstName + " " + this.state.lastName,
                        eventType: this.state.eventType,
                        guestNum: Number(this.state.guestNum),  
                        deliveryMethod: this.state.deliveryMethod,
                        dateForOrder: this.state.dateForOrder.format('ll'),
                        address: this.state.address,
                        email: this.state.email,
                        phone: this.state.phone,
                        message: this.state.message
                    }),
                    headers: {
                        'Content-Type': 'application/json'
                    }
                });
                const body = await result.json();

                if(body.hasOwnProperty('errors')){
                    let nested = [];
                    const errors1 = body.errors.map(error => {
                        if(!error.hasOwnProperty('nestedErrors')){
                            return error.msg;
                        } else {
                            nested = [...nested, ...error.nestedErrors];
                            return error.msg; 
                        }
                    })
                    const errors2 = nested.map(error => {
                        return error.msg
                    })
                    const errors = [...errors1,...errors2]
                    this.setState({
                        bErrors: errors,
                        submit: false
                    }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
                } else {
                    this.setState({
                        firstName: '',
                        lastName: '',
                        eventType: '',
                        guestNum: '',
                        deliveryMethod: '',
                        dateForOrder: null,
                        focused: false,
                        address: ['','','',''],
                        email: '',
                        phone: '',
                        message: '',
                        touched: {},
                        bErrors: undefined,
                        submit: false
                    });
            
                    this.props.confirmation(body, "catering");
                    this.props.history.push("/confirmation");
                }
            }else{
                this.setState({
                    submit:false
                })
            }
        })
    }

    handleChange(e) {
        if(e.target.name === "deliveryMethod" && e.target.value === "Pick Up"){
            let errors = this.state.errors;
            delete errors.address0;
            delete errors.address1;
            delete errors.address2;
            delete errors.address3;

            this.setState({
                address: ['','','',''],
                errors: errors
            });
        } 


        this.setState({
            [e.target.name]: e.target.value,
            touched: {...this.state.touched, [e.target.name]: true}
        });
    }

    handleArrayChange(num, e){
        let stateName = e.target.name;
        let array = this.state[stateName];
        array[num] = e.target.value;

        this.setState({
            [e.target.name]: [...array],
            touched: {...this.state.touched, [`address${num}`]: true}
        });
    }

    validate(){
        const errors = {}
        
        if(this.state.touched.firstName){
            if (this.state.firstName.trim() === "") {
            errors.firstName = 'Required';
            } else if (this.state.firstName.length < 3) {
            errors.firstName = 'Must be at least 3 characters';
            } else if (this.state.firstName.length > 30) {
                errors.firstName = 'Must be less than 30 characters';
            } else if (!/^[A-Za-z\s]+$/.test(this.state.firstName)) {
                errors.firstName = 'Must contain letters only';
            }
        }

        if(this.state.touched.lastName){
            if (this.state.lastName.trim() === "") {
                errors.lastName = 'Required';
            } else if (this.state.lastName.length < 3) {
                errors.lastName = 'Must be at least 3 characters';
            } else if (this.state.lastName.length > 30) {
                errors.lastName = 'Must be less than 30 characters';
            } else if (!/^[A-Za-z\s]+$/.test(this.state.lastName)) {
                errors.lastName = 'Must contain letters only';
            }
        }

        if(this.state.touched.eventType){
            if (this.state.eventType.trim() === "") {
                errors.eventType = 'Required';
            } else if (this.state.eventType.length < 3) {
                errors.eventType = 'Must be at least 3 characters';
            } else if (this.state.eventType.length > 30) {
                errors.eventType= 'Must be less than 30 characters';
            } else if (!/^[A-Za-z\s]+$/.test(this.state.eventType)) {
                errors.eventType = 'Must contain letters only';
            }
        }

        if(this.state.touched.guestNum){
            if (this.state.guestNum.trim() === "") {
                errors.guestNum = 'Required';
            } else if (!/^\d+$/.test(this.state.guestNum.trim())) {
                errors.guestNum = 'Numbers Only';
            } else if (this.state.guestNum.length > 3) {
                errors.guestNum = 'Must be less than 3 digits';
            }
        }

        if(this.state.touched.deliveryMethod){
            if(this.state.deliveryMethod === ""){
                errors.deliveryMethod = 'Required';
            }
        }

        if(this.state.deliveryMethod === "Delivery"){
            if(this.state.touched.address0){
                if (this.state.address[0].trim() === "") {
                    errors.address0 = 'Required';
                } else if (this.state.address[0].length < 5) {
                    errors.address0 = 'Must be at least 5 characters';
                } else if (this.state.address[0].length > 65) {
                    errors.address0 = 'Must be less than 65 characters';
                } else if (!/^[A-Za-z0-9.,'\-\s]+$/.test(this.state.address[0])) {
                    errors.address = 'Invalid Address';
                }
            }

            if(this.state.touched.address1){
                if (this.state.address[1].trim() === "") {
                    errors.address1 = 'Required';
                } else if (this.state.address[1].length < 3) {
                    errors.address1 = 'Must be at least 3 characters';
                } else if (this.state.address[1].length > 30) {
                    errors.address1 = 'Must be less than 30 characters';
                } else if (!/^[A-Za-z.,'\-\s]+$/.test(this.state.address[1])) {
                    errors.address1 = 'Invalid City';
                }
            }

            if(this.state.touched.address2){
                if (this.state.address[2].trim() === "") {
                    errors.address2 = 'Required';
                } else if (this.state.address[2].length !== 2) {
                    errors.address2 = 'Must be at least 2 characters (State abbreviation)';
                } else if (!/^[A-Za-z]+$/.test(this.state.address[2])) {
                    errors.address2 = 'Must contain letters only';
                }
            }

            if(this.state.touched.address3){
                if (this.state.address[3].trim() === "") {
                    errors.address3 = 'Required';
                } else if (this.state.address[3].length !== 5) {
                    errors.address3 = 'Must be 5 digits';
                } else if (!/^[0-9]+$/.test(this.state.address[3])) {
                    errors.address3 = 'Must contain numbers only';
                }
            }
        }

        if(this.state.touched.email){
            if (this.state.email.trim() === "") {
                errors.email = 'Required';
            } else if (this.state.email.length < 5) {
                errors.email = 'Must be at least 5 characters';
            } else if (this.state.email.length > 65) {
                errors.email = 'Must be less than 65 characters';
            }else if (!EmailValidator.validate(this.state.email)) {
                errors.email = 'Invalid email address';
            }
        }

        if(this.state.touched.phone){
            if (this.state.phone.trim() === "") {
                errors.phone = 'Required'
            } else if (this.state.phone < 10) {
                errors.phone = 'Must be at least 10 characters';
            } else if (!/^[+]?[1]?[\s]?[(]?[0-9]{3}[)]?[.-\s]?[0-9]{3}[.-\s]?[0-9]{4}$/.test(this.state.phone)) {
                errors.phone = 'Invalid Phone format'
            }
        }

        if(this.state.touched.message){
            if (this.state.message.trim() === "") {
                    errors.message = 'Required';
            } else if (this.state.message.length < 10) {
                    errors.message = 'Must be at least 10 characters';
            } else if (this.state.message.length > 300) {
                    errors.message = 'Must be less than 300 characters';
            } else if (!/^[A-Za-z0-9\W\s]+$/.test(this.state.message)) {
                    errors.message = 'Invalid Message';
            }
        }

        this.setState({
            errors: errors
        });
    }

    componentDidMount() {
        document.title = 'Catering - The Recommended Dose';
    }

    render(){

        let blockedDates = [moment()];
        if (this.props.blockedDates !== undefined){
            blockedDates = [moment(), ...this.props.blockedDates];
        }

        return(
            <>
            <h1 id="ctrHeader">Catering</h1>

            <h2 className="ctrDesc">Fill out the form and we will contact you back</h2>

            {this.state.bErrors !== undefined?
                <div ref={this.scrollDiv} id="ctrBErrors">{this.state.bErrors
                    .filter(error => !error.includes("Invalid value"))
                    .map((error,key) => (
                    <div key={key}> {error}</div>
                ))}
                </div>
            : null}

            <div  className="ctrFormWrapper">
                <div className="ctrFormContainer">
                    <form id="ctrForm" onSubmit={(e) =>  this.addCateringOrder(e)}>
                        <div className="ctrDiv">
                            <div className="ctrDiv2">
                                <label className="ctrLabel">First Name</label>
                                <input
                                type="text" 
                                className="ctrTextbox"
                                name="firstName"
                                value={this.state.firstName}
                                onChange={this.handleChange}
                                onBlur={this.validate}
                                required/>
                                {this.state.touched.firstName && this.state.errors !== undefined? 
                                <span>{this.state.errors.firstName}</span>: null}
                            </div>
                            <div className="ctrDiv2">
                                <label className="ctrLabel">Last Name</label>
                                <input 
                                type="text" 
                                className="ctrTextbox"
                                name="lastName"
                                value={this.state.lastName}
                                onChange={this.handleChange}
                                onBlur={this.validate}
                                required/>
                                {this.state.touched.lastName && this.state.errors !== undefined? 
                                <span>{this.state.errors.lastName}</span>: null}
                            </div>
                        </div>
                        <div className="ctrDiv">
                            <div className="ctrDiv2">
                                <label className="ctrLabel">Type of Event</label>
                                <input
                                type="text" 
                                className="ctrTextbox"
                                name="eventType"
                                value={this.state.eventType}
                                onChange={this.handleChange}
                                onBlur={this.validate}
                                required/>
                                {this.state.touched.eventType && this.state.errors !== undefined? 
                                <span>{this.state.errors.eventType}</span>: null}
                            </div>
                            <div className="ctrDiv2">
                                <label className="ctrLabel">Expected Number of Guests</label>
                                <input
                                type="number" 
                                className="ctrTextbox"
                                name="guestNum"
                                value={this.state.guestNum}
                                onChange={this.handleChange}
                                onBlur={this.validate}
                                required/>
                                {this.state.touched.guestNum && this.state.errors !== undefined? 
                                <span>{this.state.errors.guestNum}</span>: null}
                            </div>
                        </div>
                        <div className="ctrDiv">
                            <div className="ctrDiv2">
                                <label className="ctrLabel">Pick Up or Delivery</label>
                                <select
                                className="ctrSelect"
                                name="deliveryMethod"
                                value={this.state.deliveryMethod}
                                onChange={this.handleChange}
                                onBlur={this.validate}
                                required
                                >
                                    <option value="" disabled>Select an option</option>
                                    <option value="Pick Up">Pick Up</option>
                                    <option value="Delivery">Delivery</option>
                                </select>
                                {this.state.touched.deliveryMethod && this.state.errors !== undefined? 
                                <span>{this.state.errors.deliveryMethod}</span>: null}
                            </div>
                            <div className="ctrDiv2">
                                <label>Date For Order</label>
                                <SingleDatePicker
                                    date={this.state.dateForOrder}
                                    onDateChange={date => this.setState({ dateForOrder: date })} 
                                    focused={this.state.focused} 
                                    onFocusChange={({ focused }) => this.setState({ focused: focused })}
                                    id="ctrDate"
                                    openDirection="up"
                                    daySize={45}
                                    numberOfMonths={1}
                                    readOnly={true}
                                    block={true}
                                    isDayBlocked={day1 => blockedDates.some(day2 => isSameDay(day1, moment(day2, "MM-DD-YYYY")))}
                                />
                                {this.state.dateForOrder === null && this.state.touched.dateForOrder !== undefined? 
                                <span>Required</span>: null}
                            </div> 
                        </div>
                        {this.state.deliveryMethod === "Delivery"? (
                            <SlideDown className={'my-dropdown-slidedown'}>
                                <div className="ctrDivST">
                                    <label className="ctrLabel">Event Address</label>
                                    <input
                                    type="text"  
                                    id="ctrAddress" 
                                    className="ctrTextbox"
                                    name="address"
                                    value={this.state.address[0]}
                                    onChange={(e) => this.handleArrayChange(0, e)}
                                    onBlur={this.validate}
                                    required/>
                                    {this.state.touched.address0 && this.state.errors!== undefined? 
                                    <span>{this.state.errors.address0}</span>: null}
                                </div> 
                                <div className="ctrDiv">
                                    <div className="ctrDiv3">
                                        <label className="ctrLabel">City</label>
                                        <input
                                        type="text"   
                                        className="ctrTextbox2"
                                        name="address"
                                        value={this.state.address[1]}
                                        onChange={(e) => this.handleArrayChange(1, e)}
                                        onBlur={this.validate}
                                        required/>
                                        {this.state.touched.address1 && this.state.errors!== undefined? 
                                        <span>{this.state.errors.address1}</span>: null}
                                    </div>
                                    <div className="ctrDiv3">
                                        <label className="ctrLabel">State</label>
                                        <input
                                        type="text"   
                                        className="ctrTextbox2"
                                        name="address"
                                        value={this.state.address[2]}
                                        onChange={(e) => this.handleArrayChange(2, e)}
                                        onBlur={this.validate}
                                        required/>
                                        {this.state.touched.address2 && this.state.errors!== undefined? 
                                        <span>{this.state.errors.address2}</span>: null}
                                    </div> 
                                    <div className="ctrDiv3">
                                        <label className="ctrLabel">Zip Code</label>
                                        <input
                                        type="text"   
                                        className="ctrTextbox2"
                                        name="address"
                                        value={this.state.address[3]}
                                        onChange={(e) => this.handleArrayChange(3, e)}
                                        onBlur={this.validate}
                                        required/>
                                        {this.state.touched.address3 && this.state.errors !== undefined? 
                                        <span>{this.state.errors.address3}</span>: null}
                                    </div>  
                                </div>
                            </SlideDown>
                        ): <SlideDown className={'my-dropdown-slidedown'}></SlideDown>}

                        <div className="ctrDiv">
                            <div className="ctrDiv2">
                                <label className="ctrLabel">Email</label>
                                <input
                                type="email" 
                                className="ctrTextbox"
                                name="email"
                                value={this.state.email}
                                onChange={this.handleChange}
                                onBlur={this.validate}
                                required/>
                                {this.state.touched.email && this.state.errors !== undefined? 
                                <span>{this.state.errors.email}</span>: null}
                            </div>
                            <div className="ctrDiv2">
                                <label className="ctrLabel">Phone Number</label>
                                <input
                                type="text" 
                                className="ctrTextbox"
                                name="phone"
                                value={this.state.phone}
                                onChange={this.handleChange}
                                onBlur={this.validate}
                                required/>
                                {this.state.touched.phone && this.state.errors !== undefined? 
                                <span>{this.state.errors.phone}</span>: null}
                            </div>
                        </div>

                        <div className="ctrTextAreaDiv">
                            <label className="ctrLabel">Tell Us What You Need</label>
                            <textarea
                            type="text"  
                            className="ctrTextArea"
                            name="message"
                            value={this.state.message}
                            onChange={this.handleChange}
                            onBlur={this.validate}
                            required/>
                            {this.state.touched.message && this.state.errors!== undefined? 
                            <span>{this.state.errors.message}</span>: null}
                        </div>

                        <button type="submit" id="ctrButton"
                        style={{pointerEvents: this.state.submit === true? "none": "auto", 
                        opacity: this.state.submit === true? "0.5": "1"}}>
                            {this.state.submit === false? "Submit": <ImSpinner2 className="iconSubmitLoad"/>}
                        </button>
                    </form>
                </div>
            </div>
            <div id="ctrbottomSpace"></div>
            </>
        )
    }
}


export default withRouter(Catering);