import React, {Component} from 'react';
import "../css/FAQ.css";
import {SlideDown} from 'react-slidedown';
import 'react-slidedown/lib/slidedown.css';
import {BsChevronCompactDown} from 'react-icons/bs';
import {BsChevronCompactUp} from 'react-icons/bs';

class FAQSubsection extends Component {
    constructor() {
        super();
        this.state = {
          open: false
        };

        this.handleAccordion = this.handleAccordion.bind(this);
      }

    handleAccordion(){
        this.setState({
            open: !this.state.open
        })
    }

    render(){
        return(
            <div className="faqDiv2">
                <div className="faqQues" onClick={() => this.handleAccordion()}>
                    <h3>
                        {this.props.question}
                    </h3>
                    {this.state.open? <BsChevronCompactUp className="faqIcon"/>: 
                    <BsChevronCompactDown className="faqIcon"/>}
                </div>
                    <SlideDown>
                    {this.state.open?
                        <p className="faqAns">{this.props.answer}</p>
                    :null}
                    </SlideDown>
            </div>
        )
    }
}


export default FAQSubsection;