import React, {Component} from 'react';
import {FaWindowClose} from "react-icons/fa";
import ManagementNav from "../AdminComponents/ManagementNav";
import { withAuth0 } from '@auth0/auth0-react';
import {ImSpinner2} from "react-icons/im";

class ManageFAQ extends Component {

    constructor() {
        super();
        this.state = {
          category: '',
          question: '',
          answer: '',
          faq: [],
          item: {},
          submit: false
        };
    
        this.addFaqItem = this.addFaqItem.bind(this);
        this.deleteFaqItem = this.deleteFaqItem.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.displayItem = this.displayItem.bind(this);
        this.closeItem = this.closeItem.bind(this);
        this.scrollDiv = React.createRef();
      }

    addFaqItem = async (e) => {
        e.preventDefault();

        this.setState({
            submit: true
        });

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});
        
        const result = await fetch('/api/manage/addFAQItem', {
            method: 'post',
            body: JSON.stringify({
                category: this.state.category,
                question: this.state.question, 
                answer: this.state.answer}),
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            const errors = body.errors.map(error => {
                return error.msg;
            })
            this.setState({
                bErrors: errors,
                submit: false
            }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
        } else {
            this.setState({
                faq: body.reverse(),
                category: '',
                question: '',
                answer: '',
                bErrors: undefined,
                submit: false
            });
        }
    }

    displayItem(index){
        let tempItem = this.state.faq[index]

        this.setState({
            item: tempItem
        });
    }

    closeItem(){
        this.setState({
            item: {}
        });
    }

    handleChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    deleteFaqItem = async (id) => {

        this.closeItem();

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch(`/api/manage/delFAQItem/${id}`, {
            method: 'delete',
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        this.setState({
            faq: body.reverse(),
        });
    }

    componentDidMount() {
        const fetchData = async () => {
          const result = await fetch(`/api/faq/getFAQ`)
          const body = await result.json();
          this.setState({
            faq: body.reverse()
          });
        }
        fetchData()
    }

    render(){
        return(
            <>
                <ManagementNav/>
                <h1 id="mngHeader">FAQ Management</h1>
                
                <h2 className="mngSubHeader">Manage FAQ</h2>

                <div id="container">
                    <table>
                        <thead>
                        <tr>
                            <th>Category</th>
                            <th>Question</th>
                        </tr>
                        </thead>
                        <tbody>
                            {this.state.faq.map((item, key) => (
                                <tr key={key} onClick={() => this.displayItem(this.state.faq.indexOf(item))}> 
                                    <td>{item.category}</td>
                                    <td>{item.question}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>

                {Object.keys(this.state.item).length !== 0?
                <div className="mngOrderWrapper" onClick={() => this.closeItem()}>
                    <div className="mngOrderWindow">
                        <FaWindowClose className="mngOWIcon" onClick={() => this.closeItem()}/>
                        <div><span className="mngItemValue">Category:</span> {this.state.item.category}</div>
                        <div><span className="mngItemValue">Question:</span> {this.state.item.question}</div>
                        <br/>
                        <div><span className="mngItemValue">Answer:</span> {this.state.item.answer}</div> 
                        <button className="mngDelete" onClick={() => this.deleteFaqItem(this.state.item._id)}>Delete</button>
                    </div>  
                </div>
                :null}
                
                <h2 className="mngSubHeader">Add to FAQ</h2>

                {this.state.bErrors !== undefined?
                    <div ref={this.scrollDiv} id="coBErrors">{this.state.bErrors
                        .filter(error => !error.includes("Invalid value"))
                        .map((error,key) => (
                        <div key={key}> {error}</div>
                    ))}
                    </div>
                : null}

                <div  className="mngFormWrapper">
                    <div className="mngFormContainer">
                        <form className="mngForm" onSubmit={(e) => this.addFaqItem(e)}>
                            <div className="mngFormDivST">
                                <label className="mngLabel">Category</label>
                                <select
                                    className="mngMenuSelect"
                                    name="category"
                                    value={this.state.category}
                                    onChange={this.handleChange}
                                    required
                                >
                                    <option value="" disabled>Select an option</option>
                                    <option value="Delivery">Delivery</option>
                                    <option value="Pick Up">Pick Up</option>
                                    <option value="Ordering">Ordering</option>
                                    <option value="Allergy And Nutrition">Allergy &amp; Nutrition</option>
                                    <option value="Other">Other</option>
                                </select>
                            </div>
                            <div className="mngFormDivST">
                                <label className="mngLabel">Question</label>
                                <input 
                                    type="text"
                                    className="mngSingleTextbox"
                                    name="question"
                                    value={this.state.question}
                                    onChange={this.handleChange}
                                    required
                                />
                            </div>

                            <div className="mngTextAreaDiv">
                                <label className="mngLabel">Answer</label>
                                <textarea 
                                    className="mngTextArea"
                                    name="answer"
                                    value={this.state.answer}
                                    onChange={this.handleChange}
                                    required
                                />
                            </div>
                            
                            <button type="submit" className="mngButton"
                            style={{pointerEvents: this.state.submit === true? "none": "auto", 
                            opacity: this.state.submit === true? "0.5": "1"}}>
                                {this.state.submit === false? "Submit": <ImSpinner2 className="iconSubmitLoad"/>}
                            </button>
                        </form>
                    </div>
                </div>

                <div id="mngbottomSpace"></div>
            </>
        )
    }
}


export default withAuth0(ManageFAQ);