import React, {Component} from 'react';
import "../css/Manage.css";
import {FaWindowClose} from "react-icons/fa";
import {BsCircleFill} from "react-icons/bs";
import {ImSpinner2} from "react-icons/im";
import ManagementNav from "../AdminComponents/ManagementNav";
import FileUpload from "../AdminComponents/FileUpload";
import { withAuth0 } from '@auth0/auth0-react';

class ManageRecipes extends Component {
    _isMounted = false;

    constructor() {
        super();
        this.state = {
          recipeName: '',
          estTime: '',
          servings: '',
          description: '',
          ingredients: '',
          directions: '',
          bonusTips: '',
          recipes: [],
          item: {},
          submit: [false,false,false]
        };
    
        this.addRecipe = this.addRecipe.bind(this);
        this.deleteRecipe = this.deleteRecipe.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.displayItem = this.displayItem.bind(this);
        this.closeItem = this.closeItem.bind(this);
        this.updateRecipes = this.updateRecipes.bind(this);
        this.scrollDiv = React.createRef();
      }

    addRecipe = async (e) => {
        e.preventDefault();

        this.setState({
            submit: [true, false, false]
        });

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch('/api/manage/addRecipe', {
            method: 'post',
            body: JSON.stringify({
                recipeName: this.state.recipeName,
                estTime: this.state.estTime,
                servings: Number(this.state.servings),
                description: this.state.description,
                ingredients: this.state.ingredients, 
                directions: this.state.directions,
                bonusTips: this.state.bonusTips
            }),
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            const errors = body.errors.map(error => {
                return error.msg;
            })
            this.setState({
                bErrors: errors,
                submit: [false, false, false] 
            }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
        } else {
            this.setState({
                recipes: body.reverse(),
                recipeName: '',
                estTime: '',
                servings: '',
                description: '',
                ingredients: '',
                directions: '',
                bonusTips: '',
                bErrors: undefined,
                submit: [false, false, false] 
            });
        }
    }

    displayItem(index){
        let tempItem = this.state.recipes[index]

        this.setState({
            item: tempItem
        });
    }

    closeItem(){
        this.setState({
            item: {}
        });
    }

    updateRecipes(recipes){
        this.setState({
            recipes: recipes,
            item: {}
        })
    }



    handleChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    deleteImage = async (id) => {

        this.setState({
            submit: [false, true, false],
            delImgError: undefined
        })

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch(`/api/recipes/delImage/${id}`, {
            method: 'delete',
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            this.setState({
              delImgError: body.errors.msg,
              submit: [false, false, false]
            })
        }else{
            const newItem = body.find(item => {
                return item._id === id;
            });
            this.setState({
                recipes: body.reverse(),
                item: newItem,
                submit: [false, false, false],
                delImgError: undefined
            });
        }
    }

    deleteRecipe = async (id) => {

        this.setState({
            submit: [false, false, true],
            delItemError: undefined,
        });

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch(`/api/manage/delRecipe/${id}`, {
            method: 'delete',
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            this.setState({
              delItemError: body.errors.msg,
              submit: [false, false, false]
            })
        }else{
            this.closeItem();
            this.setState({
                recipes: body.reverse(),
                submit: [false, false, false],
                delItemError: undefined
            });
        }
    }

    componentDidMount() {
        this._isMounted = true;

        const fetchData = async () => {
          const result = await fetch(`/api/manage/getRecipes`)
          const body = await result.json();
          if (this._isMounted) {
                this.setState({
                    recipes: body.reverse()
                });
            }
        }
        fetchData()
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    render(){
        return(
            <>
                <ManagementNav/>
                <h1 id="mngHeader">Recipes Management</h1>
                
                <h2 className="mngSubHeader">Manage Recipes</h2>

                <div id="container">
                    <table>
                        <thead>
                        <tr>
                            <th>Recipe Name</th>
                            <th>Estimated Time</th>
                            <th>Servings</th>
                            <th>Has an Image?</th>
                        </tr>
                        </thead>
                        <tbody>
                            {this.state.recipes.map((item, key) => (
                                <tr key={key} onClick={() => this.displayItem(this.state.recipes.indexOf(item))}> 
                                    <td>{item.recipeName}</td>
                                    <td>{item.estTime}</td>
                                    <td>{item.servings}</td>
                                    <td className="mngImgIndicator">
                                        <BsCircleFill style={{color: item.imageKey === undefined? "red": "green"}}/>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>

                {Object.keys(this.state.item).length !== 0?
                <div className="mngOrderWrapper" >
                    <div className="mngOrderWindow">
                        <FaWindowClose className="mngOWIcon" onClick={() => this.closeItem()}/>
                        <div><span className="mngItemValue">Recipes Name:</span> {this.state.item.recipeName}</div>
                        <div><span className="mngItemValue">Estimated Time:</span> {this.state.item.estTime}</div>
                        <div><span className="mngItemValue">Servings:</span> {this.state.item.servings}</div>
                        <div><span className="mngItemValue">Description:</span> {this.state.item.description}</div>
                        <br/>
                        <div><span className="mngItemValue">Ingredients:</span> 
                            {this.state.item.ingredients.split("\n").map((i,key) => {
                                return <div key={key}>- {i}</div>;
                            })}
                        </div>
                        <br/>
                        <div><span className="mngItemValue">Directions:</span> 
                            {this.state.item.directions.split("\n").map((i,key) => {
                                return <div key={key}>- {i}</div>;
                            })}
                        </div>
                        <br/>
                        <div><span className="mngItemValue">Bonus Tips:</span> 
                            {this.state.item.bonusTips.split("\n").map((i,key) => {
                                return <div key={key}>{i}<br/></div>;
                            })}
                        </div> 
                        {this.state.item.imageKey === undefined?
                            <FileUpload 
                            itemID={this.state.item._id} 
                            collection={"recipes"}
                            updateList={this.updateRecipes}/>
                        : 
                            <>
                            {this.state.delImgError !== undefined? 
                                <div className="mngImgError">{this.state.delImgError}</div>
                            : null}
                            <button className="mngDelete" 
                            onClick={() => this.deleteImage(this.state.item._id)}
                            style={{pointerEvents: this.state.submit[1] === true? "none": "auto", 
                            opacity: this.state.submit[1] === true? "0.5": "1"}}>
                                {this.state.submit[1] === false? "Delete Image": <ImSpinner2 className="iconSubmitLoad"/>}
                            </button>
                        </>} 
                        {this.state.delItemError !== undefined? <div className="mngImgError">{this.state.delItemError}</div>: null}
                        <button className="mngDelete" 
                        onClick={() => this.deleteRecipe(this.state.item._id)}
                        style={{pointerEvents: this.state.submit[2] === true? "none": "auto", 
                        opacity: this.state.submit[2] === true? "0.5": "1"}}>
                            {this.state.submit[2] === false? "Delete Recipe": <ImSpinner2 className="iconSubmitLoad"/>}
                        </button>
                    </div>  
                </div>
                :null}
                
                <h2 className="mngSubHeader">Add a Recipe</h2>

                {this.state.bErrors !== undefined?
                    <div ref={this.scrollDiv} id="coBErrors">{this.state.bErrors
                        .filter(error => !error.includes("Invalid value"))
                        .map((error,key) => (
                        <div key={key}> {error}</div>
                    ))}
                    </div>
                : null}

                <div  className="mngFormWrapper">
                    <div className="mngFormContainer">
                        <form className="mngForm" onSubmit={(e) => this.addRecipe(e)}>
                            <div className="mngFormDivST">
                                <label className="mngLabel">Recipe Name</label>
                                <input 
                                    type="text" 
                                    className="mngSingleTextbox"
                                    name="recipeName" 
                                    value={this.state.recipeName}
                                    onChange={this.handleChange}
                                    required
                                />
                            </div>
                            <div className="mngMenuFormDiv">
                                <div className="mngMenuFormDiv2">
                                    <label className="mngLabel">Estimated Time</label>
                                    <input 
                                        type="text"
                                        className="mngMenuTextbox"
                                        name="estTime"
                                        value={this.state.estTime}
                                        onChange={this.handleChange}
                                        required
                                    />
                                </div>
                                <div className="mngMenuFormDiv2">
                                    <label className="mngLabel">Number of Servings</label>
                                    <input 
                                        type="number" 
                                        className="mngMenuTextbox"
                                        name="servings" 
                                        value={this.state.servings}
                                        onChange={this.handleChange}
                                        required
                                    />
                                </div>
                            </div>

                            <div className="mngTextAreaDiv">
                                <label className="mngLabel">Description</label>
                                <textarea 
                                    className="mngTextArea"
                                    name="description"
                                    value={this.state.description}
                                    onChange={this.handleChange}
                                    required
                                />
                            </div>

                            <div className="mngTextAreaDiv">
                                <label className="mngLabel">Ingredients (Separate each ingredient with a new line)</label>
                                <textarea 
                                    className="mngTextArea"
                                    name="ingredients"
                                    value={this.state.ingredients}
                                    onChange={this.handleChange}
                                    required
                                />
                            </div>

                            <div className="mngTextAreaDiv">
                                <label className="mngLabel">Directions (Separate each direction with new line)</label>
                                <textarea 
                                    className="mngTextArea"
                                    name="directions"
                                    value={this.state.directions}
                                    onChange={this.handleChange}
                                    required
                                />
                            </div>

                            <div className="mngTextAreaDiv">
                                <label className="mngLabel">Bonus Tips (optional)</label>
                                <textarea 
                                    className="mngTextArea"
                                    name="bonusTips"
                                    value={this.state.bonusTips}
                                    onChange={this.handleChange}
                                />
                            </div>
                            
                            <button type="submit" className="mngButton"
                            style={{pointerEvents: this.state.submit[0] === true? "none": "auto", 
                            opacity: this.state.submit[0] === true? "0.5": "1"}}>
                                {this.state.submit[0] === false? "Submit": <ImSpinner2 className="iconSubmitLoad"/>}
                            </button>
                        </form>
                    </div>
                </div>

                <div id="mngbottomSpace"></div>
            </>
        )
    }
}


export default withAuth0(ManageRecipes);