import React, {Component} from 'react';
import "../css/Manage.css";
import {FaWindowClose} from "react-icons/fa";
import {SlideDown} from 'react-slidedown';
import 'react-slidedown/lib/slidedown.css';
import { AiFillCaretDown } from 'react-icons/ai';
import { withAuth0 } from '@auth0/auth0-react';

class CustomOrderWindow extends Component {

    constructor() {
        super();
        this.state = {
          textQuery: '',
          category: '_id',
          showWindow: false,
          order: {}
        };

        this.handleChange = this.handleChange.bind(this);
        this.deleteOrderItem = this.deleteOrderItem.bind(this);
        this.displayOrder = this.displayOrder.bind(this);
        this.closeOrder = this.closeOrder.bind(this);
      }

    deleteOrderItem = async (id) => {

        this.closeOrder();

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch(`/api/manage/del/customOrders/${id}`, {
            method: 'delete',
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();
        
        this.props.updateOrders(body);
    }

    displayOrder(index){
        let tempOrder = this.props.customOrders[index]

        this.setState({
            order: tempOrder
        });
    }

    closeOrder(){
        this.setState({
            order: {}
        });
    }

    handleChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    render(){

        let filteredOrders = this.props.customOrders;
        
        filteredOrders = filteredOrders.filter(eachItem => {
            return(
              eachItem[this.state.category].toLowerCase()
              .includes(this.state.textQuery.toLowerCase())
            )
          })
        
        
        return( 
            <>   
            <div className="mngOrdersHeader"><h2 id="mngCustomHeader" className="mngSubHeader" onClick={() => 
                (this.setState({
                    showWindow: !this.state.showWindow
                }))}>Manage Custom Orders <AiFillCaretDown className="mngOrdersIcon"/>
                </h2>
            </div>
            
            {this.state.showWindow?
                <SlideDown className={'my-dropdown-slidedown'}>
                    <div id="mngSearchWrapper">
                        <div>
                            <label className="mngLabel">Search By</label>   
                            <select
                                className="mngSearchSelect"
                                name="category"
                                value={this.state.category}
                                onChange={this.handleChange}
                                required
                            >
                                <option value="_id">ID</option>
                                <option value="name">Name</option>
                                <option value="email">E-mail</option>
                                <option value="phone">Phone</option>
                            </select>
                        </div>
                        <div>
                            <label className="mngLabel">Search</label>
                            <input 
                                type="text" 
                                className="mngSearchTextbox"
                                name="textQuery" 
                                value={this.state.textQuery}
                                onChange={this.handleChange}
                                required
                            />
                        </div>
                    </div>
                                
                    <div id="container">
                        <table>
                            <thead>
                            <tr>
                                <th>ID</th>
                                <th>Name</th>
                                <th>E-Mail</th>
                                <th>Phone</th>
                                <th>Order Placed On</th>
                            </tr>
                            </thead>
                            <tbody>
                                {filteredOrders.map((order, key) => (
                                    <tr key={key} onClick={() => this.displayOrder(this.props.customOrders.indexOf(order))}> 
                                        <td>{order._id}</td>
                                        <td>{order.name}</td>
                                        <td>{order.email}</td>
                                        <td>{order.phone}</td>
                                        <td>{order.orderTime}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </SlideDown>
            : <SlideDown className={'my-dropdown-slidedown'}></SlideDown>}

            {Object.keys(this.state.order).length !== 0?
                <div className="mngOrderWrapper" onClick={() => this.closeOrder()}>
                    <div className="mngOrderWindow">
                        <FaWindowClose className="mngOWIcon" onClick={() => this.closeOrder()}/>
                        <div><span className="mngItemValue">Order Information</span></div>
                        <div><span className="mngItemValue">Order ID:</span> {this.state.order._id}</div>
                        <div><span className="mngItemValue">Order Placed On:</span> {this.state.order.orderTime}</div>
                        <div><span className="mngItemValue">Delivery Method:</span> {this.state.order.deliveryMethod}</div>
                        <div><span className="mngItemValue">
                                Selected Date For {this.state.order.deliveryMethod === "Delivery"? "Delivery": "Pick Up"}:
                            </span> {this.state.order.dateForOrder}
                        </div>
                        <br/>
                        <div><span className="mngItemValue">Order Details</span></div>
                        <div><span className="mngItemValue">Order Type:</span> {this.state.order.orderType}</div>
                        {this.state.order.orderType === "Cake"?
                        <>  
                        <div><span className="mngItemValue">Type of Cake:</span> {this.state.order.orderDetails[0]}</div>
                        <div><span className="mngItemValue">Cake  
                            {this.state.order.orderDetails[0] === "Standard Cake"? " Size":
                            this.state.order.orderDetails[0] === "Shape Cake"? " Shape":
                            this.state.order.orderDetails[0] === "Letter Cake"? " Letter":
                            this.state.order.orderDetails[0] === "Number Cake"? " Number": null}:</span> 
                            {this.state.order.orderDetails[0] === "Letter Cake"? " "+this.state.order.orderDetails[1].toUpperCase()
                            :" "+this.state.order.orderDetails[1]}
                        </div>
                        <div><span className="mngItemValue">Colors:</span> {this.state.order.orderDetails[2]}</div>
                        <div><span className="mngItemValue">Additional Details:</span> {this.state.order.orderDetails[3]}</div>
                        </>                                
                        : <div><span className="mngItemValue">Message:</span> {this.state.order.orderDetails}</div>}
                        <br/>
                        <div><span className="mngItemValue">Customer Information</span></div>
                        <div><span className="mngItemValue">Name:</span> {this.state.order.name}</div>
                        <div><span className="mngItemValue">E-mail:</span> {this.state.order.email}</div>
                        <div><span className="mngItemValue">Phone:</span> {this.state.order.phone}</div>
                        {this.state.order.deliveryMethod === "Delivery"?
                            <>
                            <div><span className="mngItemValue">Address:</span> {this.state.order.address[0]}</div>   
                            {this.state.order.address[1] !== ""? 
                                <div><span className="mngItemValue">Apt/Suite/ect:</span> {this.state.order.address[1]}</div>
                            :null}
                            <div><span className="mngItemValue">City:</span> {this.state.order.address[2]}</div>
                            <div><span className="mngItemValue">State:</span> {this.state.order.address[3]}</div>
                            <div><span className="mngItemValue">Zip Code:</span> {this.state.order.address[4]}</div>
                            </>                                
                        : null}  
                        <button className="mngDelete" onClick={() => this.deleteOrderItem(this.state.order._id)}>Delete</button>
                    </div>  
                </div>
            :null}
            </>
        )
    }
}


export default withAuth0(CustomOrderWindow);