import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class RegConfirmation extends Component {

    render(){
        return(
            <>
            <h1 id="conHeader">Thank Your For Your Order!</h1>
            
            <div id="conWrapper">
              <div  id="conDeliveryInfo" className="conInfoWrapper">
                <div id="conInfoContainer">
                  <div id="conForm">
                    <h2 className="conHeader2">
                      {this.props.body.deliveryMethod === "Delivery"? "Delivery": "Pick Up"} Details
                    </h2>
                    <div>
                        <label>Order ID: </label>
                        <span>{this.props.body._id}</span>
                    </div>
                    <div className="conTextboxDiv">
                      <div>
                        <label>Name</label>
                        <span>{this.props.body.name}</span>
                      </div>
                      <div>
                        <label>{this.props.body.deliveryMethod === "Delivery"? 
                          "Delivery": "Pick Up"} Date:
                        </label>
                        <span>{this.props.body.dateForOrder}</span>
                      </div>
                    </div>
                    <div className="conTextboxDiv">
                      <div>
                        <label>Email</label>
                        <span>{this.props.body.email}</span>
                      </div>
                      <div>
                        <label>Phone</label>
                        <span>{this.props.body.phone}</span>
                      </div>
                    </div>
                    {this.props.body.deliveryMethod === "Delivery"?
                    <>
                      <div className="conTextboxDiv">
                        <div id="conAddress">
                          <label>Address</label>
                          <span>{this.props.body.address[0]}</span>
                          {this.props.body.address[1] !== ""?
                          <span>{this.props.body.address[1]}</span>
                          : null}
                          <span>{this.props.body.address[2]}, {this.props.body.address[3]} {this.props.body.address[4]} </span>
                        </div>
                      </div>
                    </>
                    : null}
                    
                    <h2 id="conSummaryHeader" className="conHeader2">
                      Order Summary
                    </h2>
                    <div id="conCartOrders">
                    {this.props.body.cart.map((cartItem, key) => (
                      <div 
                        className="conCartItem" 
                        key={key} 
                      >
                        <div className="conCartItemInfo">
                          <span className="conCartItemName">{cartItem.itemName}</span>
                          <div className="conCartItemCtrl">
                            <span className="conCartItemPrice">${cartItem.price}</span> 
                            <span>x</span>
                            <span className="conCartItemQty">{cartItem.quantity}</span>
                          </div>
                          
                        </div>
                        <img src={cartItem.url} className="conCartItemImage" alt="cartItem.itemName"/>
                      </div>
                    ))}
                    </div>

                    <div id="conTotalDiv">
                      <div className="conCost">
                        <label>Subtotal:</label>
                        <span>${this.props.body.total[0]}</span>
                      </div>
                      <div className="conCost">
                        <label>Delivery Fee:</label>
                        <span>${this.props.body.total[1]}</span>
                      </div>
                      <div id="conTotal" className="conCost">
                        <label>Total:</label>
                        <span>${this.props.body.total[2]}</span>
                      </div>
                    </div>
                    
                    <Link to="/">
                        <button id="conButton">Return To Home</button>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
            </> 
        )
    }
}


export default RegConfirmation;