import React, {Component} from 'react';

class MenuSection extends Component {
    render(){
        return(
            <>
            {this.props.section.length !== 0 ? 
              <>
              <h2 className="menuSubHeader">{this.props.title}</h2>
              <div id="menuWrapper">
                {this.props.section.map((menuItem, key) => (
                  <div 
                    className="menuItem" 
                    key={key} 
                    onClick={() => this.props.displayWindow(menuItem)}
                  >
                    <img src={menuItem.url} alt={`${menuItem.itemName}`}/>
                    <span>{menuItem.itemName}</span>
                    <span>${menuItem.price}</span>
                  </div>
                ))}
              </div>
              </>
            : null}
            </>
        )
    }
}


export default MenuSection;