import React, {Component} from 'react';
import "../css/About.css";

class About extends Component {

  constructor() {
    super();
    this.state = {
      about: ''
    };
  }

  componentDidMount() {
    document.title = 'About - The Recommended Dose';
    const fetchData = async () => {
      const result = await fetch(`/api/about/getAbout`)
      const body = await result.json();
      if(body.length !== 0){
        this.setState({
          about: body[0].about
        });
      }
    }
    fetchData()
  }

  render(){
    return(
        <>
        <h1 id="aboutHeader">About Us</h1>
        <div id="aboutWrapper">
          <div id="aboutBorder">
            {this.state.about.split("\n").map((i,key) => {
                return <p key={key}>{i}<br/></p>;
            })}
          </div>
        </div>
        </> 
    )
  }
}


export default About;