import React, {Component} from 'react';
import "../css/Menu.css";
import MenuSection from "../Components/MenuSection"
import {AiFillPlusCircle} from "react-icons/ai";
import {AiFillMinusCircle} from "react-icons/ai";
import {GrClose} from "react-icons/gr";

class Menu extends Component {

  constructor() {
    super();
    this.state = {
      item: {},
      quantity: 1,
      visible: false,
      menu: []
    };

    this.displayWindow = this.displayWindow.bind(this);
    this.subItemNum= this.subItemNum.bind(this);
    this.addItemNum = this.addItemNum.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.addToCart = this.addToCart.bind(this);
  }

  displayWindow(menuItem){
    this.setState({
      item: menuItem,
      visible: !this.state.visible
    });

    this.props.handleMenuWindow(true);
    this.props.toggleScroll(true);
  }

  handleClose(){
    this.setState({
      item: {},
      quantity: 1,
    });

    this.props.handleMenuWindow(false);
    this.props.toggleScroll(false);
  }

  subItemNum(){
    if(this.state.quantity > 1){
      this.setState({
        quantity: this.state.quantity - 1
      })
    }
  }

  addItemNum(){
    this.setState({
      quantity: this.state.quantity + 1
    })
  }

  addToCart(){
    this.props.addToCart(
      this.state.item,
      this.state.quantity);
    
    this.handleClose();
  }

  componentDidMount() {
    document.title = 'Menu - The Recommended Dose';

    const fetchData = async () => {
      const result = await fetch(`/api/menu/getMenuItem`)
      const body = await result.json();
      this.setState({
        menu: body
      });
    }
    fetchData()
  }

  render(){

    let visibility = "hide";
    if (this.props.menuWindow) {
      visibility = "show";
    }

    const cupcakes = this.state.menu.filter(item => item.category === "Cupcakes");
    const cakes = this.state.menu.filter(item => item.category === "Cakes");
    const jars = this.state.menu.filter(item => item.category === "Jars");
    const cakepops = this.state.menu.filter(item => item.category === "Cakepops");
    const other = this.state.menu.filter(item => item.category === "Other");
    const beverages = this.state.menu.filter(item => item.category === "Beverages");

    return(
      <>
        <h1 id="menuHeader">Menu For {this.props.deliveryDate}</h1>
        
        {this.props.orderMin > 0 || this.props.freeDeliveryMin > 0? 
          <div id="menuAnnouncement">
            <div>
              {this.props.orderMin > 0? 
                <span id="menuOrderMin">Orders Need To Be a Minimum of ${this.props.orderMin} to Checkout</span>
              : null}
              {this.props.freeDeliveryMin > 0? 
                <span id="menuFreeDel">Free Delivery on orders ${this.props.freeDeliveryMin}+</span>
              : null}
            </div>
          </div> : null}
        
        <MenuSection section={cupcakes} title="Cupcakes" displayWindow={this.displayWindow}/>
        <MenuSection section={cakes} title="Cakes" displayWindow={this.displayWindow}/>
        <MenuSection section={jars} title="Jars" displayWindow={this.displayWindow}/>
        <MenuSection section={cakepops} title="Cakepops" displayWindow={this.displayWindow}/>
        <MenuSection section={other} title="Other" displayWindow={this.displayWindow}/>
        <MenuSection section={beverages} title="Beverages" displayWindow={this.displayWindow}/>

        <div id="menubottomSpace"></div>

        <div id="menuItemWindow" className={visibility}>
          <GrClose id="menuClose" onClick={() => this.handleClose()}/>
          <img className="menuImg" src={this.state.item.url} alt="Menu Pic" />
          <div id="menuItemContent">
            <span>{this.state.item.itemName}</span>
            <span>${this.state.item.price}</span>
          </div>
          <span id="menuItemDesc">{this.state.item.itemDesc}</span>
          <div id="menuItemSubmit">
            <div id="menuItemQty">
            <AiFillMinusCircle className="menuQtyButton" onClick={() => this.subItemNum()}/>
            <span>{this.state.quantity}</span>
            <AiFillPlusCircle className="menuQtyButton" onClick={() => this.addItemNum()}/>
            </div>
            <button id="menuATCButton" onClick={() => this.addToCart()}>
                <span>${this.state.item.price*this.state.quantity}<br/>ADD TO CART</span>
            </button>
          </div> 
        </div>
      </>
    )
  }
}


export default Menu;