import React, {Component} from 'react';
import { withRouter } from "react-router-dom";
import "../css/CustomOrder.css";
import {SlideDown} from 'react-slidedown';
import 'react-slidedown/lib/slidedown.css';
import 'react-dates/lib/css/_datepicker.css';
import { SingleDatePicker} from 'react-dates';
import isSameDay from 'react-dates/src/utils/isSameDay';
import moment from 'moment';
import {ImSpinner2} from "react-icons/im";
import * as EmailValidator from 'email-validator';

class customOrder extends Component {

    constructor() {
        super();
        this.state = {
          orderType: '',
          cakeDetails: ['','','',''],
          otherDetails: '',
          firstName: '',
          lastName: '',
          deliveryMethod: '',
          dateForOrder: null,
          focused: false,
          address: ['','','','',''],
          email: '',
          phone: '',
          touched: {},
          errors: {},
          submit: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleArrayChange = this.handleArrayChange.bind(this);
        this.addCustomOrder = this.addCustomOrder.bind(this);
        this.validate = this.validate.bind(this);
        this.scrollDiv = React.createRef();
    }


    addCustomOrder = async (e) => {
        e.preventDefault();

        this.setState({
            touched: { orderType: true, firstName: true, lastName: true, eventType: true, guestNum: true, 
                deliveryMethod: true, dateForOrder: true, address0: true, address1: true, address2: true, 
                address3: true, address4: true, cakeDetails0: true, cakeDetails1: true, cakeDetails2: true, 
                cakeDetails3: true, otherDetails: true, email: true, phone: true, message: true,
            },
            submit: true
        }, async () => {

            this.validate();

            if(Object.keys(this.state.errors).length === 0 
            && this.state.dateForOrder !== null){
                let details;
                if(this.state.orderType === "Cake"){
                    details = this.state.cakeDetails;
                } else if (this.state.orderType === "Other"){
                    details = this.state.otherDetails;
                }

                const result = await fetch('/api/order/addCustomOrder', {
                    method: 'post',
                    body: JSON.stringify({
                        orderTime: moment().format('LLL'),
                        orderType: this.state.orderType,
                        orderDetails: details,
                        name: this.state.firstName + " " + this.state.lastName,  
                        deliveryMethod: this.state.deliveryMethod,
                        dateForOrder: this.state.dateForOrder.format('ll'),
                        address: this.state.address,
                        email: this.state.email,
                        phone: this.state.phone
                    }),
                    headers: {
                        'Content-Type': 'application/json'
                    }
                });
                const body = await result.json();

                if(body.hasOwnProperty('errors')){
                    let nested = [];
                    const errors1 = body.errors.map(error => {
                        if(!error.hasOwnProperty('nestedErrors')){
                            return error.msg;
                        } else {
                            nested = [...nested, ...error.nestedErrors];
                            return error.msg; 
                        }
                    })
                    let errors2 = []
                    if(this.state.orderType === "Cake"){
                        errors2 = nested.filter(error => error.msg.includes("Cake") || error.param.includes("address"))
                        .map(error => {
                            return error.msg
                        })
                    } else {
                        errors2 = nested.filter(error => !error.msg.includes("Cake"))
                        .map(error => {
                            return error.msg
                        })
                    }
                    const errors = [...errors1,...errors2]
                    this.setState({
                        bErrors: errors,
                        submit: false
                    }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
                } else {
                    this.setState({
                        orderType: '',
                        cakeDetails: ['','','',''],
                        otherDetails: '',
                        firstName: '',
                        lastName: '',
                        deliveryMethod: '',
                        dateForOrder: null,
                        address: ['','','','',''],
                        email: '',
                        phone: '',
                        touched: {},
                        bErrors: undefined,
                        submit: false
                    });
        
                    this.props.confirmation(body, "custom");
                    this.props.history.push("/confirmation");
                }
            } else {
                this.setState({
                    submit: false
                })
            }
        })
    }


    handleChange(e) {

        if(e.target.name === "orderType" && e.target.value === "Cake"){
            let errors = this.state.errors;
            delete errors.otherDetails;

            this.setState({
                otherDetails: '',
                errors: errors
            }, () => {
                this.validate();
            });
        } else if(e.target.name === "orderType" && e.target.value === "Other"){
            let errors = this.state.errors;
            delete errors.cakeDetails0;
            delete errors.cakeDetails1;
            delete errors.cakeDetails2;
            delete errors.cakeDetails3;

            this.setState({
                cakeDetails: ['','','',''],
                errors: errors
            }, () => {
                this.validate();
            });
        }

        if(e.target.name === "deliveryMethod" && e.target.value === "Pick Up"){
            let errors = this.state.errors;
            delete errors.address0;
            delete errors.address1;
            delete errors.address2;
            delete errors.address3;
            delete errors.address4;

            this.setState({
                address: ['','','','',''],
                errors: errors
            });
        }

        this.setState({
            [e.target.name]: e.target.value,
            touched: {...this.state.touched, [e.target.name]: true}
        });
    }

    handleArrayChange(num, e){
        let stateName = e.target.name;
        let array = this.state[stateName];

        if(e.target.name === "cakeDetails" && num === 0){
            array[1] = '';
        }

        array[num] = e.target.value;

        this.setState({
            [e.target.name]: [...array],
            touched: {...this.state.touched, [`${e.target.name}${num}`]: true}
        });
    }

    validate(){
        const errors = {}
        
        if(this.state.touched.orderType){
            if(this.state.orderType === ""){
                errors.orderType = 'Required';
            }
        }

        if(this.state.orderType === "Cake"){
            if(this.state.touched.cakeDetails0){
                if (this.state.cakeDetails[0].trim() === "") {
                    errors.cakeDetails0 = 'Required';
                } else if (!/(Number Cake)|(Standard Cake)|(Letter Cake)|(Shape Cake)/.test(this.state.cakeDetails[0])) {
                    errors.cakeDetails0 = 'Invalid';
                }
            }


            if(this.state.touched.cakeDetails1){
                if (this.state.cakeDetails[1].trim() === "") {
                    errors.cakeDetails1 = 'Required';
                } else if (this.state.cakeDetails[1].length > 30) {
                    errors.cakeDetails1 = 'Must be less than 30 characters';
                } else if (!/(6 Inches)|(8 Inches)|(10 Inches)|^[0-9]+$|^[A-Za-z/s]+$/.test(this.state.cakeDetails[1])) {
                    errors.cakeDetails1 = 'Invalid No Special Character Allowed';
                }
            }

            if(this.state.touched.cakeDetails2){
                if (this.state.cakeDetails[2].trim() === "") {
                    errors.cakeDetails2 = 'Required';
                } else if (this.state.cakeDetails[2].length < 3) {
                    errors.cakeDetails2 = 'Must be at least 3 characters';
                } else if (this.state.cakeDetails[2].length > 30) {
                    errors.cakeDetails2 = 'Must be less than 30 characters';
                } else if (!/^[A-Za-z]+$/.test(this.state.cakeDetails[2])) {
                    errors.cakeDetails2 = 'Must contain letters only';
                }
            }

            if(this.state.touched.cakeDetails3){
                if (this.state.cakeDetails[3].trim() === "") {
                        errors.cakeDetails3 = 'Required';
                } else if (this.state.cakeDetails[3].length < 10) {
                        errors.cakeDetails3 = 'Must be at least 10 characters';
                } else if (this.state.cakeDetails[3].length > 300) {
                        errors.cakeDetails3 = 'Must be less than 300 characters';
                } else if (!/^[A-Za-z0-9\W\s]+$/.test(this.state.cakeDetails[3])) {
                        errors.cakeDetails3 = 'Invalid Message';
                }
            }
        }

        if(this.state.orderType === "Other"){
            if(this.state.touched.otherDetails){
                if (this.state.otherDetails.trim() === "") {
                    errors.otherDetails = 'Required';
                } else if (this.state.otherDetails.length < 10) {
                    errors.otherDetails = 'Must be at least 10 characters';
                } else if (this.state.otherDetails.length > 300) {
                        errors.otherDetails = 'Must be less than 300 characters';
                }else if (!/^[A-Za-z0-9\W\s]+$/.test(this.state.otherDetails)) {
                    errors.otherDetails = 'Invalid Message';
                }
            }
        }

        if(this.state.touched.firstName){
            if (this.state.firstName.trim() === "") {
                errors.firstName = 'Required';
            } else if (this.state.firstName.length < 3) {
                errors.firstName = 'Must be at least 3 characters';
            } else if (this.state.firstName.length > 30) {
                errors.firstName = 'Must be less than 30 characters';
            } else if (!/^[A-Za-z\s]+$/.test(this.state.firstName)) {
                errors.firstName = 'Must contain letters only';
            }
        }

        if(this.state.touched.lastName){
            if (this.state.lastName.trim() === "") {
                errors.lastName = 'Required';
            } else if (this.state.lastName.length < 3) {
                errors.lastName = 'Must be at least 3 characters';
            } else if (this.state.lastName.length > 30) {
                errors.lastName = 'Must be less than 30 characters';
            } else if (!/^[A-Za-z\s]+$/.test(this.state.lastName)) {
                errors.lastName = 'Must contain letters only';
            }
        }


        if(this.state.touched.deliveryMethod){
            if(this.state.deliveryMethod === ""){
                errors.deliveryMethod = 'Required';
            }
        }

        if(this.state.deliveryMethod === "Delivery"){
            if(this.state.touched.address0){
                if (this.state.address[0].trim() === "") {
                    errors.address0 = 'Required';
                } else if (this.state.address[0].length < 5) {
                    errors.address0 = 'Must be at least 5 characters';
                } else if (this.state.address[0].length > 65) {
                    errors.address0 = 'Must be less than 65 characters';
                } else if (!/^[A-Za-z0-9.,'\-\s]+$/.test(this.state.address[0])) {
                    errors.address0 = 'Invalid Address';
                }
            }

            if (!/(^$)|(^[A-Za-z0-9.,'\-\s]+$)/.test(this.state.address[1])) {
                errors.address1 = 'Invalid character in use';
            } else if (this.state.address[1].length > 15) {
                errors.address0 = 'Must be less than 15 characters';
            }

            if(this.state.touched.address2){
                if (this.state.address[2].trim() === "") {
                    errors.address2 = 'Required';
                } else if (this.state.address[2].length < 5) {
                    errors.address2 = 'Must be at least 3 characters';
                } else if (this.state.address[2].length > 30) {
                    errors.address2 = 'Must be less than 30 characters';
                } else if (!/^[A-Za-z.,'\-\s]+$/.test(this.state.address[2])) {
                    errors.address2 = 'Invalid City';
                }
            }

            if(this.state.touched.address3){
                if (this.state.address[3].trim() === "") {
                    errors.address3 = 'Required';
                } else if (this.state.address[3].length !== 2) {
                    errors.address3 = 'Must be at 2 character (State abbreviation)';
                } else if (!/^[A-Za-z]+$/.test(this.state.address[3])) {
                    errors.address3 = 'Must contain letters only';
                }
            }

            if(this.state.touched.address4){
                if (this.state.address[4].trim() === "") {
                    errors.address4 = 'Required';
                } else if (this.state.address[4].length !== 5) {
                    errors.address4 = 'Must be 5 digits';
                } else if (!/^[0-9]+$/.test(this.state.address[4])) {
                    errors.address4 = 'Must Contain Numbers only';
                }
            }
        }

        if(this.state.touched.email){
            if (this.state.email.trim() === "") {
                errors.email = 'Required';
            } else if (this.state.email.length < 5) {
                errors.email = 'Must be at least 5 characters';
            } else if (this.state.email.length > 65) {
                errors.email = 'Must be less than 65 characters';
            }else if (!EmailValidator.validate(this.state.email)) {
                errors.email = 'Invalid email address';
            }
        }

        if(this.state.touched.phone){
            if (this.state.phone.trim() === "") {
                errors.phone = 'Required'
            } else if (this.state.phone < 10) {
                errors.phone = 'Must be at least 10 characters';
            } else if (!/^[+]?[1]?[\s]?[(]?[0-9]{3}[)]?[.-\s]?[0-9]{3}[.-\s]?[0-9]{4}$/.test(this.state.phone)) {
                errors.phone = 'Invalid Phone format'
            }
        }

        this.setState({
            errors: errors
        });
    }

    componentDidMount() {
        document.title = 'Custom Order - The Recommended Dose';
    }

    render(){

        let blockedDates = [moment()];
        if (this.props.blockedDates !== undefined){
            blockedDates = [moment(), ...this.props.blockedDates];
        }

        return(
            <>
            <h1 id="cOrderHeader">Custom Order</h1>

            <h2 className="cOrderDesc">Fill out the form and we will contact you back</h2>

            {this.state.bErrors !== undefined?
                <div ref={this.scrollDiv} id="cOrderBErrors">{this.state.bErrors
                    .filter(error => !error.includes("Invalid value"))
                    .map((error,key) => (
                    <div key={key}> {error}</div>
                ))}
                </div>
            : null}

            <div  className="cOrderFormWrapper">
                <div className="cOrderFormContainer">
                    <form id="cOrderForm" onSubmit={(e) => this.addCustomOrder(e)}>
                        <h3 className="cOrderDesc">Order Info</h3>
                        <section id="cOrderInfoSection">
                            <div id="cOrderInfo" onChange={this.handleChange}>
                                <h4>What Would You Like?</h4>
                                <label className="cOrderRadio">
                                    <input 
                                    type="radio" 
                                    id="cakeRadio" 
                                    name="orderType" 
                                    value="Cake"
                                    required
                                />Cake
                                </label>
                                <label className="cOrderRadio">
                                    <input 
                                    type="radio" 
                                    name="orderType" 
                                    value="Other"
                                />Something Else
                                </label>
                            </div>
                            {this.state.touched.orderType && this.state.errors.orderType !== undefined? 
                            <span id="cOrderError" >{this.state.errors.orderType}</span>: null}

                        {this.state.orderType === "Cake"? (
                            <SlideDown className={'my-dropdown-slidedown'}>
                                <div id="cakeInfo">
                                    <div className="cOrderDivST">
                                        <label>Type?</label>
                                        <select
                                        className="cOrderSelect"
                                        name="cakeDetails"
                                        value={this.state.cakeDetails[0]}
                                        onChange={(e) => this.handleArrayChange(0, e)}
                                        required
                                        >
                                            <option value="" disabled>Select A Size</option>
                                            <option value="Standard Cake">Standard Cake</option>
                                            <option value="Shape Cake">Shape Cake</option>
                                            <option value="Letter Cake">Letter Cake</option>
                                            <option value="Number Cake">Number Cake</option>
                                        </select>
                                        {this.state.touched.cakeDetails0 && this.state.errors.cakeDetails0 !== undefined? 
                                        <div id="cOrderError">{this.state.errors.cakeDetails0}</div>: null}
                                    </div>

                                    {this.state.cakeDetails[0] === "Standard Cake"? (
                                        <div className="cOrderDivST">
                                            <label>Size?</label>
                                            <select
                                            className="cOrderSelect"
                                            name="cakeDetails"
                                            value={this.state.cakeDetails[1]}
                                            onChange={(e) => this.handleArrayChange(1, e)}
                                            onBlur={this.validate}
                                            required
                                            >
                                                <option value="" disabled>Select an option</option>
                                                <option value="6 Inches">6 Inches</option>
                                                <option value="8 Inches">8 Inches</option>
                                                <option value="10 Inches">10 Inches</option>
                                            </select>
                                            {this.state.touched.cakeDetails1 && this.state.errors.cakeDetails1 !== undefined? 
                                            <span id="cOrderError">{this.state.errors.cakeDetails1}</span>: null}
                                        </div>
                                    ): this.state.cakeDetails[0] === "Shape Cake"?(
                                        <div className="cOrderDivST">
                                            <label>What Shape?</label>
                                            <input
                                            type="text" 
                                            className="cOrderTextbox"
                                            name="cakeDetails"
                                            value={this.state.cakeDetails[1]}
                                            onChange={(e) => this.handleArrayChange(1, e)}
                                            onBlur={this.validate}
                                            required/>
                                            {this.state.touched.cakeDetails1 && this.state.errors.cakeDetails1 !== undefined? 
                                            <div id="cOrderError">{this.state.errors.cakeDetails1}</div>: null}
                                        </div>
                                    ): this.state.cakeDetails[0] === "Letter Cake"?(
                                        <div className="cOrderDivST">
                                            <label>What Letter?</label>
                                            <input 
                                            type="text"
                                            className="cOrderTextbox"
                                            name="cakeDetails"
                                            value={this.state.cakeDetails[1]}
                                            onChange={(e) => this.handleArrayChange(1, e)}
                                            onBlur={this.validate}
                                            required/>
                                            {this.state.touched.cakeDetails1 && this.state.errors.cakeDetails1 !== undefined? 
                                            <div id="cOrderError">{this.state.errors.cakeDetails1}</div>: null}
                                        </div>
                                    ): this.state.cakeDetails[0] === "Number Cake"?(
                                        <div className="cOrderDivST">
                                            <label>What Number?</label>
                                            <input 
                                            type="number"
                                            className="cOrderTextbox"
                                            name="cakeDetails"
                                            value={this.state.cakeDetails[1]}
                                            onChange={(e) => this.handleArrayChange(1, e)}
                                            onBlur={this.validate}
                                            required/>
                                            {this.state.touched.cakeDetails1 && this.state.errors.cakeDetails1 !== undefined? 
                                            <div id="cOrderError">{this.state.errors.cakeDetails1}</div>: null}
                                        </div>
                                    ):<SlideDown className={'my-dropdown-slidedown'}></SlideDown>}
                                    <div className="cOrderDivST">
                                        <label>Color(s)?</label>
                                        <input 
                                        className="cOrderTextbox"
                                        name="cakeDetails"
                                        value={this.state.cakeDetails[2]}
                                        onChange={(e) => this.handleArrayChange(2, e)}
                                        onBlur={this.validate}
                                        required/>
                                        {this.state.touched.cakeDetails2 && this.state.errors.cakeDetails2 !== undefined? 
                                        <div id="cOrderError">{this.state.errors.cakeDetails2}</div>: null}
                                    </div>

                                    <div className="cOrderTextAreaDiv">
                                        <label>Additional Details</label>
                                        <textarea 
                                        className="cOrderTextArea"
                                        name="cakeDetails"
                                        value={this.state.cakeDetails[3]}
                                        onChange={(e) => this.handleArrayChange(3, e)}
                                        onBlur={this.validate}
                                        required/>
                                        {this.state.touched.cakeDetails3 && this.state.errors.cakeDetails3 !== undefined? 
                                        <div id="cOrderError">{this.state.errors.cakeDetails3}</div>: null}
                                    </div>
                                </div>
                            </SlideDown>
                            ):
                        this.state.orderType === "Other"? (
                            <SlideDown className={'my-dropdown-slidedown'}>
                                <div id="otherInfo">
                                    <label>Describe What You Would Like</label>
                                    <textarea 
                                    className="cOrderTextArea"
                                    name="otherDetails"
                                    value={this.state.otherDetails}
                                    onChange={this.handleChange}
                                    onBlur={this.validate}
                                    required/>
                                    {this.state.touched.otherDetails && this.state.errors.otherDetails !== undefined? 
                                    <div id="cOrderError">{this.state.errors.otherDetails}</div>: null}
                                </div>
                            </SlideDown>
                            ): null}
                        </section>

                        <h3 className="cOrderDesc">Contact and Delivery Info</h3>

                        <section id="cOrderDeliverySection">
                            <div className="cOrderDiv">
                                <div className="cOrderDiv2">
                                    <label className="cOrderLabel">First Name</label>
                                    <input
                                    type="text" 
                                    className="cOrderTextbox"
                                    name="firstName"
                                    value={this.state.firstName}
                                    onChange={this.handleChange}
                                    onBlur={this.validate}
                                    required/>
                                    {this.state.touched.firstName && this.state.errors.firstName !== undefined? 
                                    <span>{this.state.errors.firstName}</span>: null}
                                </div>
                                <div className="cOrderDiv2">
                                    <label className="cOrderLabel">Last Name</label>
                                    <input
                                    type="text" 
                                    className="cOrderTextbox"
                                    name="lastName"
                                    value={this.state.lastName}
                                    onChange={this.handleChange}
                                    onBlur={this.validate}
                                    required/>
                                    {this.state.touched.lastName && this.state.errors.lastName !== undefined? 
                                    <span>{this.state.errors.lastName}</span>: null}
                                </div>
                            </div>
                            <div className="cOrderDiv">
                                <div className="cOrderDiv2">
                                    <label className="cOrderLabel">Pick Up or Delivery</label>
                                    <select
                                    className="cOrderSelect"
                                    name="deliveryMethod"
                                    value={this.state.deliveryMethod}
                                    onChange={this.handleChange}
                                    onBlur={this.validate}
                                    required
                                    >
                                        <option value="" disabled>Select an option</option>
                                        <option value="Pick Up">Pick Up</option>
                                        <option value="Delivery">Delivery</option>
                                    </select>
                                    {this.state.touched.deliveryMethod && this.state.errors.deliveryMethod !== undefined? 
                                    <span>{this.state.errors.deliveryMethod}</span>: null}
                                </div>
                                <div className="cOrderDiv2">
                                    <label className="cOrderLabel">Date For Order</label>
                                    <SingleDatePicker
                                        date={this.state.dateForOrder}
                                        onDateChange={date => this.setState({ dateForOrder: date })} 
                                        focused={this.state.focused} 
                                        onFocusChange={({ focused }) => this.setState({ focused: focused })}
                                        id="cOrderDate"
                                        block={true}
                                        openDirection="up"
                                        daySize={45}
                                        numberOfMonths={1}
                                        readOnly={true}
                                        isDayBlocked={day1 => blockedDates.some(day2 => isSameDay(day1, moment(day2, "MM-DD-YYYY")))}
                                    />
                                    {this.state.dateForOrder === null && this.state.touched.dateForOrder !== undefined? 
                                    <span>Required</span>: null}
                                </div>
                            </div>
                            {this.state.deliveryMethod === "Delivery"? (
                                <SlideDown className={'my-dropdown-slidedown'}>
                                    <div className="cOrderDiv">
                                        <div className="cOrderDiv2">
                                            <label className="cOrderLabel">Address</label>
                                            <input
                                            type="text" 
                                            className="cOrderTextbox"
                                            name="address"
                                            value={this.state.address[0]}
                                            onChange={(e) => this.handleArrayChange(0, e)}
                                            onBlur={this.validate}
                                            required/>
                                            {this.state.touched.address0 && this.state.errors.address0 !== undefined? 
                                            <span>{this.state.errors.address0}</span>: null}
                                        </div>
                                        <div className="cOrderDiv2">
                                            <label className="cOrderLabel">Apartment, Suite, Ect. (Optional)</label>
                                            <input
                                            type="text"  
                                            className="cOrderTextbox"
                                            name="address"
                                            value={this.state.address[1]}
                                            onChange={(e) => this.handleArrayChange(1, e)}
                                            onBlur={this.validate}/>
                                            {this.state.touched.address1 && this.state.errors.address1 !== undefined? 
                                            <span>{this.state.errors.address1}</span>: null}
                                        </div>
                                    </div>
                                    <div className="cOrderDiv">
                                        <div className="cOrderDiv3">
                                            <label className="cOrderLabel">City</label>
                                            <input 
                                            type="text"  
                                            className="cOrderTextbox2"
                                            name="address"
                                            value={this.state.address[2]}
                                            onChange={(e) => this.handleArrayChange(2, e)}
                                            onBlur={this.validate}
                                            required/>
                                            {this.state.touched.address2 && this.state.errors.address2 !== undefined? 
                                            <span>{this.state.errors.address2}</span>: null}
                                        </div>
                                        <div className="cOrderDiv3">
                                            <label className="cOrderLabel">State</label>
                                            <input
                                            type="text"  
                                            className="cOrderTextbox2"
                                            name="address"
                                            value={this.state.address[3]}
                                            onChange={(e) => this.handleArrayChange(3, e)}
                                            onBlur={this.validate}
                                            required/>
                                            {this.state.touched.address3 && this.state.errors.address3 !== undefined? 
                                            <span>{this.state.errors.address3}</span>: null}
                                        </div>
                                        <div className="cOrderDiv3">
                                            <label className="cOrderLabel">Zip Code</label>
                                            <input
                                            type="number"  
                                            className="cOrderTextbox2"
                                            name="address"
                                            value={this.state.address[4]}
                                            onChange={(e) => this.handleArrayChange(4, e)}
                                            onBlur={this.validate}
                                            required/>
                                            {this.state.touched.address4 && this.state.errors.address4 !== undefined? 
                                            <span>{this.state.errors.address4}</span>: null}
                                        </div>
                                    </div>
                                </SlideDown>
                            ): <SlideDown className={'my-dropdown-slidedown'}></SlideDown>}
                            <div className="cOrderDiv">
                                <div className="cOrderDiv2">
                                    <label className="cOrderLabel">Email</label>
                                    <input 
                                    type= "email"
                                    className="cOrderTextbox"
                                    name="email"
                                    value={this.state.email}
                                    onChange={this.handleChange}
                                    onBlur={this.validate}
                                    required/>
                                    {this.state.touched.email && this.state.errors.email !== undefined? 
                                    <span>{this.state.errors.email}</span>: null}
                                </div>
                                <div className="cOrderDiv2">
                                    <label className="cOrderLabel">Phone Number</label>
                                    <input
                                    type= "number"
                                    className="cOrderTextbox"
                                    name="phone"
                                    value={this.state.phone}
                                    onChange={this.handleChange}
                                    onBlur={this.validate}
                                    required/>
                                    {this.state.touched.phone && this.state.errors.phone !== undefined? 
                                    <span>{this.state.errors.phone}</span>: null}
                                </div>
                            </div>
                        </section>

                        <button type="submit" id="cOrderButton"
                        style={{pointerEvents: this.state.submit === true? "none": "auto", 
                        opacity: this.state.submit === true? "0.5": "1"}}>
                            {this.state.submit === false? "Place Order": <ImSpinner2 className="iconSubmitLoad"/>}
                        </button>

                    </form>
                </div>
            </div>

            <div id="cOrderbottomSpace"></div>
            </>
        )
    }
}


export default withRouter(customOrder);