import React, {Component} from 'react';
import "../css/Manage.css";
import {FaWindowClose} from "react-icons/fa";
import {BsCircleFill} from "react-icons/bs";
import {ImSpinner2} from "react-icons/im";
import ManagementNav from "../AdminComponents/ManagementNav";
import FileUpload from "../AdminComponents/FileUpload";
import { withAuth0 } from '@auth0/auth0-react';

class ManageMenu extends Component {
    _isMounted = false;

    constructor() {
        super();
        this.state = {
          category: '',
          itemName: '',
          price: '',
          itemDesc: '',
          menu: [],
          item: {},
          submit: [false,false,false]
        };
    
        this.addMenuItem = this.addMenuItem.bind(this);
        this.deleteMenuItem = this.deleteMenuItem.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.displayItem = this.displayItem.bind(this);
        this.closeItem = this.closeItem.bind(this);
        this.updateMenu = this.updateMenu.bind(this);
        this.scrollDiv = React.createRef();
      }

    addMenuItem = async (e) => {
        e.preventDefault();

        this.setState({
            submit: [true, false, false]
        });

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch('/api/manage/addMenuItem', {
            method: 'post',
            body: JSON.stringify({
                category: this.state.category,
                itemName: this.state.itemName,
                itemDesc: this.state.itemDesc, 
                price: Number(this.state.price),
                }),
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            const errors = body.errors.map(error => {
                return error.msg;
            })
            this.setState({
                bErrors: errors,
                submit: [false, false, false] 
            }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
        } else {
            this.setState({
                menu: body.reverse(),
                category: '',
                itemName: '',
                price: '',
                itemDesc: '',
                bErrors: undefined,
                submit: [false, false, false]
            });
        }
        
    }

    displayItem(index){
        let tempItem = this.state.menu[index]

        this.setState({
            item: tempItem
        });
    }

    closeItem(){
        this.setState({
            item: {}
        });
    }

    updateMenu(menu){
        this.setState({
            menu: menu,
            item: {}
        })
    }

    handleChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    deleteImage = async (id) => {

        this.setState({
            submit: [false, true, false],
            delImgError: undefined
        })

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch(`/api/menu/delImage/${id}`, {
            method: 'delete',
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            this.setState({
              delImgError: body.errors.msg,
              submit: [false, false, false]
            })
        }else{
            const newItem = body.find(item => {
                return item._id === id;
            });
            this.setState({
                menu: body.reverse(),
                item: newItem,
                submit: [false, false, false],
                delImgError: undefined
            });
        }
    }

    deleteMenuItem = async (id) => {

        this.setState({
            submit: [false, false, true],
            delItemError: undefined,
        })

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch(`/api/manage/delMenuItem/${id}`, {
            method: 'delete',
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            this.setState({
              delItemError: body.errors.msg,
              submit: [false, false, false]
            })
        }else{
            this.closeItem();
            this.setState({
                menu: body.reverse(),
                submit: [false, false, false],
                delItemError: undefined
            });
        }
    }

    componentDidMount() {
        this._isMounted = true;
        
        const fetchData = async () => {
          const result = await fetch(`/api/manage/getMenuItem`);
          const body = await result.json();
          if (this._isMounted) {
                this.setState({
                    menu: body.reverse()
                });
            }
        }
        fetchData()
    }

    componentWillUnmount() {
        this._isMounted = false;
    }

    render(){
        return(
            <>
                <ManagementNav/>
                <h1 id="mngHeader">Menu Management</h1>
                
                <h2 className="mngSubHeader">Manage Menu</h2>
                <div id="container">
                    <table>
                        <thead>
                        <tr>
                            <th>Category</th>
                            <th>Item</th>
                            <th>Price</th>
                            <th>Has An Image?</th>
                        </tr>
                        </thead>
                        <tbody>
                            {this.state.menu.map((item, key) => (
                                <tr key={key} onClick={() => this.displayItem(this.state.menu.indexOf(item))}> 
                                    <td>{item.category}</td>
                                    <td>{item.itemName}</td>
                                    <td>${item.price}</td>
                                    <td className="mngImgIndicator">
                                        <BsCircleFill style={{color: item.imageKey === undefined? "red": "green"}}/>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>

                {Object.keys(this.state.item).length !== 0?
                    <div className="mngOrderWrapper">
                        <div className="mngOrderWindow">
                            <FaWindowClose className="mngOWIcon" onClick={() => this.closeItem()}/>
                            <div><span className="mngItemValue">Category:</span> {this.state.item.category}</div>
                            <div><span className="mngItemValue">Item:</span> {this.state.item.itemName}</div>
                            <div><span className="mngItemValue">Cost:</span> ${this.state.item.price}</div>
                            <div><span className="mngItemValue">Description:</span> {this.state.item.itemDesc}</div>
                            {this.state.item.imageKey === undefined?
                                <FileUpload 
                                itemID={this.state.item._id} 
                                collection={"menu"}
                                updateList={this.updateMenu}/>
                            : 
                                <>
                                {this.state.delImgError !== undefined? 
                                <div className="mngImgError">{this.state.delImgError}</div>
                                : null}
                                <button className="mngDelete" 
                                    onClick={() => this.deleteImage(this.state.item._id)}
                                    style={{pointerEvents: this.state.submit[1] === true? "none": "auto", 
                                    opacity: this.state.submit[1] === true? "0.5": "1"}}>
                                        {this.state.submit[1] === false? "Delete Image": <ImSpinner2 className="iconSubmitLoad"/>}
                                </button>
                            </>} 

                            {this.state.delItemError !== undefined? <div className="mngImgError">{this.state.delItemError}</div>: null}
                            <button className="mngDelete" 
                            onClick={() => this.deleteMenuItem(this.state.item._id)}
                            style={{pointerEvents: this.state.submit[2] === true? "none": "auto", 
                            opacity: this.state.submit[2] === true? "0.5": "1"}}>
                                {this.state.submit[2] === false? "Delete Menu Item": <ImSpinner2 className="iconSubmitLoad"/>}
                            </button>
                        </div>  
                    </div>
                :null}
                
                <h2 className="mngSubHeader">Add to Menu</h2>

                {this.state.bErrors !== undefined?
                    <div ref={this.scrollDiv} id="coBErrors">{this.state.bErrors
                        .filter(error => !error.includes("Invalid value"))
                        .map((error,key) => (
                        <div key={key}> {error}</div>
                    ))}
                    </div>
                : null}

                <div  className="mngFormWrapper">
                    <div className="mngFormContainer">
                        <form className="mngForm" onSubmit={(e) => this.addMenuItem(e)}>
                            <div className="mngFormDivST">
                                <label className="mngLabel">Category</label>   
                                <select
                                    name="category"
                                    className="mngMenuSelect"
                                    value={this.state.category}
                                    onChange={this.handleChange}
                                    required
                                >
                                    <option value="" disabled>Select an option</option>
                                    <option value="Cupcakes">Cupcakes</option>
                                    <option value="Cakes">Cakes</option>
                                    <option value="Jars">Jars</option>
                                    <option value="Cakepops">Cakepops</option>
                                    <option value="Other">Other</option>
                                    <option value="Beverages">Beverages</option>
                                </select>
                            </div>
                            <div className="mngMenuFormDiv">
                                <div className="mngMenuFormDiv2">
                                    <label className="mngLabel">Item Name</label>
                                    <input 
                                        type="text" 
                                        className="mngMenuTextbox"
                                        name="itemName" 
                                        value={this.state.itemName}
                                        onChange={this.handleChange}
                                        required
                                    />
                                </div>
                                <div className="mngMenuFormDiv2">
                                    <label className="mngLabel">Price</label>
                                    <input 
                                        type="number"
                                        className="mngMenuTextbox"
                                        name="price"
                                        value={this.state.price}
                                        onChange={this.handleChange}
                                        required
                                    />
                                </div>
                            </div>

                            <div className="mngTextAreaDiv">
                                <label className="mngLabel">Item Description</label>
                                <textarea 
                                    className="mngTextArea"
                                    name="itemDesc"
                                    value={this.state.itemDesc}
                                    onChange={this.handleChange}
                                    required
                                />
                            </div>
                            
                            <button type="submit" className="mngButton"
                            style={{pointerEvents: this.state.submit[0] === true? "none": "auto", 
                            opacity: this.state.submit[0] === true? "0.5": "1"}}>
                                {this.state.submit[0] === false? "Submit": <ImSpinner2 className="iconSubmitLoad"/>}
                            </button>
                        </form>
                    </div>
                </div>

                <div id="mngbottomSpace"></div>
            </>
        )
    }
}


export default withAuth0(ManageMenu);