import React, {Component} from 'react';
import "../css/Confirmation.css";
import RegConfirmation from "../Components/CheckoutConfirmation";
import CusConfirmation from "../Components/CustomOrderConfirmation";
import CatConfirmation from "../Components/CateringConfirmation";
import CUConfirmation from "../Components/ContactConfirmation";

class Confirmation extends Component {

  componentDidMount() {
    document.title = 'Confirmation - The Recommended Dose';
  }

  render(){
    return(
        <>

        {this.props.type === "regular"?
          <RegConfirmation body={this.props.body}/>
        : this.props.type === "custom"?
          <CusConfirmation body={this.props.body}/>
        : this.props.type === "catering"?
          <CatConfirmation body={this.props.body}/>
        : this.props.type === "contact"?
          <CUConfirmation body={this.props.body}/>
        : null}
        
        <div id="conBottomSpace"></div>
        </> 
    )
  }
}


export default Confirmation;