import React, {Component} from 'react';
import 'react-dates/initialize';
import 'react-dates/lib/css/_datepicker.css';
import { SingleDatePicker} from 'react-dates';
import isSameDay from 'react-dates/src/utils/isSameDay';
import moment from 'moment';
import {FaWindowClose} from "react-icons/fa";
import ManagementNav from "../AdminComponents/ManagementNav";
import { withAuth0 } from '@auth0/auth0-react';
import {ImSpinner2} from "react-icons/im";

class ManageOther extends Component {

    constructor() {
        super();
        this.state = {
          newDeliveryAmount: '',
          deliveryAmount: 0,
          newOrderMin: '',
          orderMin: 0,
          newFreeDeliveryMin: '',
          freeDeliveryMin: 0,
          deliveryDate: '',
          newDate: null,
          focused: false,
          blockedDates: [], 
          newBlockedDates: null,
          blockedFocused: false,
          menuPageVis: null,
          submit: [false,false,false,false,false,false]
        };
    
        this.handleChange = this.handleChange.bind(this);
        this.scrollDiv = React.createRef();
    }

    updateMPVis = async () => {

        this.setState({
            submit: [true,false,false,false,false,false]
        })
        
        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch('/api/manage/updateMenuPageToggle', {
            method: 'put',
            body: JSON.stringify({toggle: !this.state.menuPageVis}),
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            const errors = body.errors.map(error => {
                return error.msg;
            })
            this.setState({
                bErrors: errors,
                submit: [false,false,false,false,false,false]
            }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
        } else {
            this.setState({
                menuPageVis: body[0].toggle,
                bErrors: undefined,
                submit: [false,false,false,false,false,false]
            });
        }
    }

    updateDeliveryAmount = async () => {
        
        this.setState({
            submit: [false,true,false,false,false,false]
        });

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch('/api/manage/updateDeliveryAmount', {
            method: 'put',
            body: JSON.stringify({amount: Number(this.state.newDeliveryAmount)}),
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            const errors = body.errors.map(error => {
                return error.msg;
            })
            this.setState({
                bErrors: errors,
                submit: [false,false,false,false,false,false]
            }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
        } else {
            this.setState({
                deliveryAmount: body[0].amount,
                newDeliveryAmount: '',
                bErrors: undefined,
                submit: [false,false,false,false,false,false]
            });
        }
    }

    updateOrderMin = async () => {
        
        this.setState({
            submit: [false,false,true,false,false,false]
        })

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch('/api/manage/updateOrderMin', {
            method: 'put',
            body: JSON.stringify({minimum: Number(this.state.newOrderMin)}),
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            const errors = body.errors.map(error => {
                return error.msg;
            })
            this.setState({
                bErrors: errors,
                submit: [false,false,false,false,false,false]
            }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
        } else {
            this.setState({
                orderMin: body[0].minimum,
                newOrderMin: '',
                bErrors: undefined,
                submit: [false,false,false,false,false,false]
            });
        }
    }

    updateFreeDeliveryMin = async () => {

        this.setState({
            submit: [false,false,false,true,false,false]
        })
        
        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

        const result = await fetch('/api/manage/updateFreeDeliveryMin', {
            method: 'put',
            body: JSON.stringify({minimum: Number(this.state.newFreeDeliveryMin)}),
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            const errors = body.errors.map(error => {
                return error.msg;
            })
            this.setState({
                bErrors: errors,
                submit: [false,false,false,false,false,false]
            }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
        } else {
            this.setState({
                freeDeliveryMin: body[0].minimum,
                newFreeDeliveryMin: '',
                bErrors: undefined,
                submit: [false,false,false,false,false,false]
            });
        }
    }

    updateDeliveryDate = async () => {

        this.setState({
            submit: [false,false,false,false,true,false]
        });

        if(this.state.newDate !== null){
            const { getAccessTokenSilently } = this.props.auth0;
            const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});

            const result = await fetch('/api/manage/updateDeliveryDate', {
                method: 'put',
                body: JSON.stringify({date: this.state.newDate.format('ll')}),
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${token}`
                }
            });
            const body = await result.json();

            if(body.hasOwnProperty('errors')){
                const errors = body.errors.map(error => {
                    return error.msg;
                })
                this.setState({
                    bErrors: errors,
                    submit: [false,false,false,false,false,false]
                }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
            } else {
                this.setState({
                    deliveryDate: body[0].date,
                    newDate: null,
                    bErrors: undefined,
                    submit: [false,false,false,false,false,false]
                });
            }
        }
    }

    updateBlockedDates = async () => {

        this.setState({
            submit: [false,false,false,false,false,true]
        });

        if(this.state.newBlockedDates !== null){
            const blockedDates1 = this.state.blockedDates;
            const blockedDates2 = blockedDates1.filter(date => moment(date, "MM-DD-YYYY").isBefore(moment(), "day") !== true);
            blockedDates2.unshift(this.state.newBlockedDates.format("MM-DD-YYYY"));

            const { getAccessTokenSilently } = this.props.auth0;
            const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});
            
            const result = await fetch('/api/manage/updateBlockedDates', {
                method: 'put',
                body: JSON.stringify({dates: blockedDates2}),
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${token}`
                }
            });
            const body = await result.json();

            if(body.hasOwnProperty('errors')){
                const errors = body.errors.map(error => {
                    return error.msg;
                })
                this.setState({
                    bErrors: errors,
                    submit: [false,false,false,false,false,false]
                }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
            } else {
                this.setState({
                    blockedDates: body[0].dates,
                    newBlockedDates: null,
                    bErrors: undefined,
                    submit: [false,false,false,false,false,false]
                });
            }
        }
    }

    deleteBlockedDate = async (index) => {

        const blockedDates = this.state.blockedDates;
        blockedDates.splice(index-1,1);

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});
        
        const result = await fetch('/api/manage/updateBlockedDates', {
            method: 'put',
            body: JSON.stringify({dates: blockedDates}),
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            const errors = body.errors.map(error => {
                return error.msg;
            })
            this.setState({
                bErrors: errors
            }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
        } else {
            this.setState({
                blockedDates: body[0].dates,
                bErrors: undefined
            });
        }
    }

    handleChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
    }
    
    componentDidMount() {
        const fetchData = async () => {
            const result = await fetch(`/api/home/getOtherSettings`)
            const body = await result.json();

            const mpv = body.filter(obj => {return obj.name === "MenuPageToggle"});
            if(mpv.length !== 0){
              this.setState({
                  menuPageVis: mpv[0].toggle
                });
            }

            const da = body.filter(obj => {return obj.name === "DeliveryAmount"});
            if(da.length !== 0){
              this.setState({
                  deliveryAmount: da[0].amount
                });
            }

            const om = body.filter(obj => {return obj.name === "OrderMin"});
            if(om.length !== 0){
              this.setState({
                  orderMin: om[0].minimum
                });
            }

            const fdm = body.filter(obj => {return obj.name === "FreeDeliveryMin"});
            if(fdm.length !== 0){
              this.setState({
                  freeDeliveryMin: fdm[0].minimum
                });
            }

            const dd = body.filter(obj => {return obj.name === "DeliveryDate"});
            if(dd.length !== 0){
              this.setState({
                  deliveryDate: dd[0].date
                });
            }

            const bd = body.filter(obj => {return obj.name === "BlockedDates"});
            if(bd.length !== 0){
              this.setState({
                  blockedDates: bd[0].dates
                });
            }
        }
        fetchData()
      }

    render(){

        let blockedDates = [moment()];
        if (this.state.blockedDates !== undefined){
            blockedDates = [moment(), ...this.state.blockedDates];
        }

        return(
            <>
                <ManagementNav/>
                <h1 id="mngHeader">Manage Other</h1>

                {this.state.bErrors !== undefined?
                    <div ref={this.scrollDiv} id="coBErrors">{this.state.bErrors
                        .filter(error => !error.includes("Invalid value"))
                        .map((error,key) => (
                        <div key={key}> {error}</div>
                    ))}
                    </div>
                : null}

                <h2 className="mngSubHeader">Toggle Order Menu Page</h2>
                <section id="mngOtherdiv">
                    <div className="mngOtherdiv2">
                        <label>Order Menu Page Visibility</label>
                        {this.state.menuPageVis?  
                            <span>Showing</span>:
                            <span>Hidden</span>
                        }
                    </div>
                    <div className="mngOtherdiv2">
                        <label>Toggle Order Page Visibility</label>
                        <button 
                        type="submit" 
                        id="mngOtherButton" 
                        onClick={() => this.updateMPVis()}
                        style={{pointerEvents: this.state.submit[0] === true? "none": "auto", 
                        opacity: this.state.submit[0] === true? "0.5": "1"}}>
                            {this.state.submit[0] === false? "Toggle": <ImSpinner2 className="iconSubmitLoad"/>}
                        </button>
                    </div>
                </section>
                
                <h2 className="mngSubHeader">Manage Delivery Amount</h2>
                <section id="mngOtherdiv">
                    <div className="mngOtherdiv2">
                        <label>Delivery Amount</label>
                        <span>${this.state.deliveryAmount}</span>
                    </div>
                    <div className="mngOtherdiv2">
                        <label>Update Delivery Amount To</label>
                            <input 
                                type="number" 
                                className="mngOtherTextbox"
                                name="newDeliveryAmount" 
                                value={this.state.newDeliveryAmount}
                                onChange={this.handleChange}
                                required
                            />
                            <button 
                            type="submit" 
                            id="mngOtherButton"
                            onClick={() => this.updateDeliveryAmount()}
                            style={{pointerEvents: this.state.submit[1] === true? "none": "auto", 
                            opacity: this.state.submit[1] === true? "0.5": "1"}}>
                                {this.state.submit[1] === false? "Update": <ImSpinner2 className="iconSubmitLoad"/>}
                            </button>
                    </div>
                </section>

                <h2 className="mngSubHeader">Manage Minimum To Order</h2>
                <section id="mngOtherdiv">
                    <div className="mngOtherdiv2">
                        <label>Minimum Amount</label>
                        <span>${this.state.orderMin}</span>
                    </div>
                    <div className="mngOtherdiv2">
                        <label>Update Minimum Amount To</label>
                            <input 
                                type="number" 
                                className="mngOtherTextbox"
                                name="newOrderMin" 
                                value={this.state.newOrderMin}
                                onChange={this.handleChange}
                                required
                            />
                            <button 
                            type="submit" 
                            id="mngOtherButton"
                            onClick={() => this.updateOrderMin()}
                            style={{pointerEvents: this.state.submit[2] === true? "none": "auto", 
                            opacity: this.state.submit[2] === true? "0.5": "1"}}>
                                {this.state.submit[2] === false? "Update": <ImSpinner2 className="iconSubmitLoad"/>}
                            </button>
                    </div>
                </section>

                <h2 className="mngSubHeader">Manage Minimum For Free Delivery</h2>
                <section id="mngOtherdiv">
                    <div className="mngOtherdiv2">
                        <label>Minimum Amount</label>
                        <span>${this.state.freeDeliveryMin}</span>
                    </div>
                    <div className="mngOtherdiv2">
                        <label>Update Minimum Amount To</label>
                            <input 
                                type="number" 
                                className="mngOtherTextbox"
                                name="newFreeDeliveryMin" 
                                value={this.state.newFreeDeliveryMin}
                                onChange={this.handleChange}
                                required
                            />
                            <button 
                            type="submit" 
                            id="mngOtherButton"
                            onClick={() => this.updateFreeDeliveryMin()}
                            style={{pointerEvents: this.state.submit[3] === true? "none": "auto", 
                            opacity: this.state.submit[3] === true? "0.5": "1"}}>
                                {this.state.submit[3] === false? "Update": <ImSpinner2 className="iconSubmitLoad"/>}
                            </button>
                    </div>
                </section>

                <h2 className="mngSubHeader">Manage Delivery Date</h2>
                <section id="mngOtherdiv">
                    <div className="mngOtherdiv2">
                        <label>Delivery Date</label>
                        <span>{this.state.deliveryDate}</span>
                    </div>
                    <div className="mngODatediv">
                        <label>Update Delivery Date To</label>
                        <SingleDatePicker
                            date={this.state.newDate}
                            onDateChange={date => this.setState({ newDate: date })} 
                            focused={this.state.focused} 
                            onFocusChange={({ focused }) => this.setState({ focused })}
                            openDirection="up"
                            daySize={45}
                            numberOfMonths={1}
                            readOnly={true}
                        />
                        <button 
                        type="submit" 
                        id="mngOtherButton" 
                        onClick={() => this.updateDeliveryDate()}
                        style={{pointerEvents: this.state.submit[4] === true? "none": "auto", 
                            opacity: this.state.submit[4] === true? "0.5": "1"}}>
                                {this.state.submit[4] === false? "Update": <ImSpinner2 className="iconSubmitLoad"/>}
                        </button>
                    </div>
                </section>

                <h2 className="mngSubHeader">Manage Blocked Dates</h2>
                <section id="mngOtherdiv">
                    <div className="mngOBDatesdiv">
                        <label>Blocked Dates</label>
                        <div id="mngOBWrapper">
                            <div className="mngBlockedDates">
                                <span>{moment(blockedDates[0], "MM-DD-YYYY").format("ll")}</span>
                            </div>
                            {blockedDates.slice(1).map((date,key) => (
                                <div className="mngBlockedDates" key={key}>
                                    <span>{moment(date, "MM-DD-YYYY").format("ll")}</span>
                                    <FaWindowClose 
                                    className="mngOBIcon" 
                                    onClick={() => this.deleteBlockedDate(blockedDates.indexOf(date))}/>
                                </div>
                            ))}
                        </div>
                    </div>
                    <div className="mngODatediv">
                        <label>Add Date to Blocked Dates</label>
                        <SingleDatePicker
                            date={this.state.newBlockedDates}
                            onDateChange={date => this.setState({ newBlockedDates: date })} 
                            focused={this.state.blockedFocused} 
                            onFocusChange={({ focused }) => this.setState({ blockedFocused: focused })}
                            openDirection="up"
                            daySize={45}
                            id="mngDate"
                            numberOfMonths={1}
                            readOnly={true}
                            isDayBlocked={day1 => blockedDates.some(day2 => isSameDay(day1, moment(day2, "MM-DD-YYYY")))}
                        />
                        <button 
                        type="submit" 
                        id="mngOtherButton" 
                        onClick={() => this.updateBlockedDates()}
                        style={{pointerEvents: this.state.submit[5] === true? "none": "auto", 
                            opacity: this.state.submit[5] === true? "0.5": "1"}}>
                                {this.state.submit[5] === false? "Update": <ImSpinner2 className="iconSubmitLoad"/>}
                        </button>
                    </div>
                </section>

                <div id="mngbottomSpace"></div>
            </>
        )
    }
}


export default withAuth0(ManageOther);