import React, {Component} from 'react';
import {GrClose} from "react-icons/gr";
import {AiFillPlusCircle} from "react-icons/ai";
import {AiFillMinusCircle} from "react-icons/ai";
import {RiDeleteBin2Fill} from "react-icons/ri"
import cakeSlice from "../images/cartCakeSlice.png";
import { withRouter } from "react-router-dom";

class Cart extends Component {

  handleClose(){
      this.props.handleCartWindow(false);
      this.props.toggleScroll(false);
      this.props.handleDeliverySelection(null);
  }

  handleAddress = () => {
    this.props.handleCartWindow(false);
    this.props.toggleScroll(false);
    this.props.history.push("/checkout");
  }

  render(){

    let selectDelivery, selectPickup = "notSelected";
    if(this.props.deliverySelection === true){
      selectDelivery = "selected";
    } else if(this.props.deliverySelection === false){
      selectPickup = "selected";
    }

    let selectOnline, selectInPerson = "notSelected";
    if(this.props.paymentMethod === true){
      selectOnline = "selected";
    } else if(this.props.paymentMethod === false){
      selectInPerson = "selected";
    }


    let visibility = "hide";
    if (this.props.cartWindow) {
      visibility = "show";
    }

    return(
        <div id="cartWindow" className={visibility}>
          <div id="cartTop">
            <GrClose id="cartClose" onClick={() => this.handleClose()}/>
            <span id="cartHeader">Your Order</span>
          </div>
          <div id="cartOrdersWrapper">
            <div id="cartOrders">
              {this.props.cart.length !== 0 && this.props.freeDeliveryMin > 0? 
                <span id="cartFreeDel">Free Delivery on orders ${this.props.freeDeliveryMin}+</span>
              : null}
              {this.props.cart.map((cartItem, key) => (
                <div 
                  className="cartItem" 
                  key={key} 
                >
                  <div className="cartItemInfo">
                    <span className="cartItemName">{cartItem.itemName}</span>
                    <span className="cartItemPrice">${cartItem.price}</span>
                    <div className="cartItemCtrl">
                      <AiFillMinusCircle 
                        className="cartItemIcon"
                        onClick={() => this.props.subQty(this.props.cart.indexOf(cartItem), cartItem.price)}
                        />
                      <span className="cartItemQty">{cartItem.quantity}</span>
                      <AiFillPlusCircle 
                        className="cartItemIcon"
                        onClick={() => this.props.addQty(this.props.cart.indexOf(cartItem), cartItem.price)}/>
                      <span 
                        className="cartItemVertLine">|</span>
                      <RiDeleteBin2Fill 
                        className="cartItemDelIcon"
                        onClick={() => this.props.deleteItem(this.props.cart.indexOf(cartItem),cartItem.price,cartItem.quantity)}/>
                    </div>
                  </div>
                  <img src={cartItem.url} className="cartItemImage" alt="Cart Item Pic"/>
                </div>
              ))}
              {this.props.cart.length === 0?
                <div id="cartEmptyDiv">
                  <span>Your Cart Is Currently Empty</span>
                  <img src={cakeSlice} id="cartEmptyImage" alt="Empty Cart Pic"/>
                </div>: null
              }
            </div>
          </div>
          <section id="cartCheckout">
            <div id="cartDeliveryDiv">
              <span>Delivery Or Pickup:</span>
              <div id="cartDeliveryOptBtn">
                <button 
                  id="cartDeliveryBtn" 
                  className={selectDelivery}
                  style={{opacity: this.props.cart.length !== 0 && this.props.total >= this.props.orderMin? "1": "0.5",
                  pointerEvents: this.props.cart.length !== 0 && this.props.total >= this.props.orderMin? "auto": "none"}}
                  onClick={() => this.props.handleDeliverySelection(true)}
                >Delivery</button>
                <button 
                  id="cartPickupBtn" 
                  className={selectPickup}
                  style={{opacity: this.props.cart.length !== 0 && this.props.total >= this.props.orderMin? "1": "0.5",
                  pointerEvents: this.props.cart.length !== 0 && this.props.total >= this.props.orderMin? "auto": "none"}}
                  onClick={() => this.props.handleDeliverySelection(false)}
                >Pickup</button>
              </div>
            </div>
            <div id="cartDeliveryDiv">
              <span>Payment Method:</span>
              <div id="cartDeliveryOptBtn">
                <button 
                  id="cartOnlineBtn" 
                  className={selectOnline}
                  style={{opacity: this.props.cart.length !== 0 && this.props.total >= this.props.orderMin? "1": "0.5",
                  pointerEvents: this.props.cart.length !== 0 && this.props.total >= this.props.orderMin? "auto": "none"}}
                  onClick={() => this.props.handlePaymentMethod(true)}
                >Online</button>
                <button 
                  id="cartInPersonBtn" 
                  className={selectInPerson}
                  style={{opacity: this.props.cart.length !== 0 && this.props.total >= this.props.orderMin? "1": "0.5",
                  pointerEvents: this.props.cart.length !== 0 && this.props.total >= this.props.orderMin? "auto": "none"}}
                  onClick={() => this.props.handlePaymentMethod(false)}
                >In Person</button>
              </div>
            </div>
            
            <div className="cartTotal">
              <span>Delivery Fee:</span>
              <span>{this.props.freeDeliveryValid? "FREE DELIVERY":
                (this.props.freeDeliveryValid === null && this.props.deliverySelection) || 
                (!this.props.freeDeliveryValid && this.props.deliverySelection)? 
                "$"+this.props.deliveryAmount: "$"+0}
              </span>
            </div>
            <div className="cartTotal">
              <span>Total:</span>
              <span>${this.props.freeDeliveryValid? this.props.total:
                (this.props.freeDeliveryValid === null && this.props.deliverySelection) || 
                (!this.props.freeDeliveryValid && this.props.deliverySelection)? 
                this.props.total + this.props.deliveryAmount: this.props.total}
              </span>
            </div>
            
            <button 
              id="cartCOButton"
              style={{pointerEvents: this.props.cart.length !== 0 
                && this.props.deliverySelection !== null
                && this.props.paymentMethod !== null 
                && this.props.total >= this.props.orderMin? "auto": "none",
                opacity: this.props.cart.length !== 0 
                && this.props.deliverySelection !== null
                && this.props.paymentMethod !== null 
                && this.props.total >= this.props.orderMin? "1": "0.5"}} 
              onClick={this.props.cart.length !== 0 
                && this.props.deliverySelection !== null
                && this.props.paymentMethod !== null 
                && this.props.total >= this.props.orderMin? 
                () => this.handleAddress(): null}>
                  <span>{this.props.total >= this.props.orderMin? "CHECKOUT": 
                    "Minimum $"+this.props.orderMin+" Order Required"}
                  </span>
            </button>
          </section>
        </div>
    )

  }

  
}


export default withRouter(Cart);