import React, {Component} from 'react';
import ManagementNav from "../AdminComponents/ManagementNav";
import { withAuth0 } from '@auth0/auth0-react';
import {ImSpinner2} from "react-icons/im";

class ManageAbout extends Component {

    constructor() {
        super();
        this.state = {
          newAbout: '',
          about: '',
          submit: false
        };

        this.handleChange = this.handleChange.bind(this);
        this.updateAbout = this.updateAbout.bind(this);
        this.scrollDiv = React.createRef();
      }

    updateAbout = async (e) => {
        e.preventDefault();

        this.setState({
            submit: true
        })

        const { getAccessTokenSilently } = this.props.auth0;
        const token = await getAccessTokenSilently({audience: process.env.REACT_APP_AUTH0_AUDIENCE});
        
        const result = await fetch('/api/manage/updateAbout', {
            method: 'put',
            body: JSON.stringify({about: this.state.newAbout}),
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        });
        const body = await result.json();

        if(body.hasOwnProperty('errors')){
            const errors = body.errors.map(error => {
                return error.msg;
            })
            this.setState({
                bErrors: errors,
                submit: false
            }, () => this.scrollDiv.current.scrollIntoView({ behavior: 'smooth' }))
        } else {
            this.setState({
                about: body[0].about,
                newAbout: '',
                bErrors: undefined,
                submit: false
            });
        }
    }

    handleChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
    }


    componentDidMount() {
        const fetchData = async () => {
          const result = await fetch(`/api/about/getAbout`)
          const body = await result.json();
          if(body.length !== 0){
            this.setState({
                about: body[0].about
              });
          }
          
        }
        fetchData()
      }
    

    render(){
        return(
            <>
                <ManagementNav/>
                
                <h1 id="mngHeader">About Management</h1>
                
                <h2 className="mngSubHeader">Manage About</h2>
                <div id="container">
                    <div className="mngAbtDiv">
                        <div><span className="mngItemValue">About:<br/>
                        </span> {this.state.about.split("\n").map((i,key) => {
                            return <p key={key}>{i}<br/></p>;
                        })}</div>       
                    </div>
                </div>
                
                <h2 className="mngSubHeader">Change About</h2>

                {this.state.bErrors !== undefined?
                    <div ref={this.scrollDiv} id="coBErrors">{this.state.bErrors
                        .filter(error => !error.includes("Invalid value"))
                        .map((error,key) => (
                        <div key={key}> {error}</div>
                    ))}
                    </div>
                : null}

                <div  className="mngFormWrapper">
                    <div className="mngFormContainer">
                        <form className="mngForm" onSubmit={(e) => this.updateAbout(e)}>
                            <div className="mngTextAreaDiv">
                                <label className="mngLabel">New About</label>
                                <textarea 
                                    className="mngTextArea"
                                    id="mngTextAreaAbt"
                                    name="newAbout"
                                    value={this.state.newAbout}
                                    onChange={this.handleChange}
                                    required
                                />
                            </div>
                            <button type="submit" className="mngButton"
                            style={{pointerEvents: this.state.submit === true? "none": "auto", 
                            opacity: this.state.submit === true? "0.5": "1"}}>
                                {this.state.submit === false? "Update": <ImSpinner2 className="iconSubmitLoad"/>}
                            </button>
                        </form>
                    </div>
                </div>

                <div id="mngbottomSpace"></div>

            </>
        )
    }
}


export default withAuth0(ManageAbout);