import React, {Component} from 'react';
import "../css/Login.css";
import { withAuth0 } from '@auth0/auth0-react';

class Login extends Component {

    componentDidMount() {
        document.title = 'Login - The Recommended Dose';
    }

    render(){

        const { isAuthenticated, loginWithRedirect } = this.props.auth0;

        return(
            <>
            {!isAuthenticated? 
                <>
                <h1 id="loginHeader">Admin Login</h1>
                    <button id="loginButton" onClick={() => loginWithRedirect({})} type="button">
                        Continue to Login
                    </button>
                </>
            : <h1 id="loginHeader">You're Currently Logged In!</h1>}
            </> 
        )
    }
}


export default withAuth0(Login);